<template>
	<div id="addcontract">
		<h5></h5>
		<el-form ref="form" :model="addcontractform" label-width="80px" size="mini">
			<table id="addcontractTab">
				<!---->
				<tr>
					<td>
						<el-form-item label="" >
				  			<el-input v-model="addcontractform.contractCode"  v-validate="'required'" name="name1"></el-input>
				  			<span class="tipsinfo" v-show="errors.has('name1')"></span>
				  		</el-form-item>
					</td>
					<td>
						<el-form-item label="">
				  			<el-input v-model="addcontractform.contractCompany" v-validate="'required'" name="name2"></el-input>
				  			<span class="tipsinfo" v-show="errors.has('name2')"></span>
				  		</el-form-item>
					</td>
				</tr>
				<!---->
				<tr>
					<td>
						<el-form-item label="" >
				  			<el-input v-model="addcontractform.projectName" v-validate="'required'" name="name3"></el-input>
				  			<span class="tipsinfo" v-show="errors.has('name3')"></span>
				  		</el-form-item>
					</td>
					<td>
						<el-form-item label="">
				  			<el-date-picker type="date" placeholder="" v-model="addcontractform.depositBackTime" ></el-date-picker>
				  		</el-form-item>
					</td>
				</tr>
				<!---->
				<tr>
					<td>
						<el-form-item label="" >
				  			<el-input v-model="addcontractform.completeAmount"></el-input>
				  		</el-form-item>
					</td>
					<td>
						<el-form-item label="">
				  			<el-input v-model="totalAmount" readonly="readonly"></el-input> <!--//addcontractform.totalQuantity-->
				  		</el-form-item>
					</td>
				</tr>
				<!---->
				<tr>
					<td>
						<el-form-item label="" >
				  			<el-input v-model="totalPrice" readonly="readonly"></el-input>
				  		</el-form-item>
					</td>
					<td>
						<el-form-item label="">
				  			<el-input v-model="totalPrix" readonly="readonly"></el-input> <!--//addcontractform.totalPrice-->
				  		</el-form-item>
					</td>
				</tr>
				<!---->
				<tr>
					<td>
						<el-form-item label="">
					    	<el-col :span="11">
					    		<el-date-picker type="date" placeholder="" v-model="addcontractform.preSignDate"></el-date-picker>
					   	 	</el-col>
				  		</el-form-item>
					</td>
					<td>
						<el-form-item label="">
					    	<el-col :span="11">
					    		<el-date-picker type="date" placeholder="" v-model="addcontractform.signDate"></el-date-picker>
					   	 	</el-col>
				  		</el-form-item>
					</td>
				</tr>
				<!---->
				
				<!---->
				<tr>
					<td>
						<el-form-item label="">
			    			<el-select v-model="addcontractform.area" placeholder="">
			      				<el-option label="" value=""></el-option>
			      				<el-option :label="item.category" :value="item.id" v-for="item in areaCategoryList" ></el-option>
			    			</el-select>
			  			</el-form-item>
					</td>
					<td>
						<el-form-item label="">
			    			<el-select v-model="addcontractform.region" placeholder="">
			      				<el-option label="" value=""></el-option>
			      				<el-option :label="item.category" :value="item.id" v-for="item in regionCategoryList" ></el-option>
			   				</el-select>
			  			</el-form-item>
					</td>
				</tr>
				<!---->
				<tr>
					<td>
						<el-form-item label="">
					    	<el-select v-model="addcontractform.taxCategory" placeholder="">
			      				<el-option label="" value=""></el-option>
			      				<el-option :label="item.category" :value="item.id" v-for="item in invoiceCategoryList" ></el-option>
			   				</el-select>
						</el-form-item>
					</td>
					<td>
						<el-form-item label="" >
				  			<el-input v-model="addcontractform.deposit"></el-input>
				  		</el-form-item>
					</td>
				</tr>
				
			</table>
			
			<div class="text-box">
				<!---->
				<el-form-item label="" class="box">
				    <el-input type="textarea"  v-model="addcontractform.note"></el-input>
				</el-form-item>
				<!---->
				<el-form-item label="" class="box">
					<el-input type="textarea"  v-model="addcontractform.comment"></el-input>
				</el-form-item>
				
				
				<el-form-item label="" id="addPerson">
				    <el-button type="primary" style="float: left;" @click="addfirst"></el-button>
				</el-form-item>
				
				
				
				<table  id="insidedata">
        					<tr >
        						<th class="firsttr" align="center" width="25%" height=20></th>
		                        <th class="firsttr" align="center" width="25%" height=20></th>
		                        <th class="firsttr" align="center" width="25%" height=20></th>
		                        <th class="firsttr" align="center" width="25%" height=20></th>
        					</tr>
        					<tr v-for="(item,index) in arrList">
        						<td class="secondtr" align="center" width="25%" height=20 >
						  			<input type="text"  class="inpu"v-model="index" readonly="readonly"/>
        						</td>
        						<td class="secondtr" align="center" width="25%" height=20>
							  		<input type="text"  class="inpu"  v-model="item.paid"/>
        						</td>
        						
        						<td class="secondtr" align="center" width="25%" height=20>
        							<el-form-item >
								    	<el-col :span="1">
								    		<el-date-picker type="date" placeholder="" v-model="item.payTime" ></el-date-picker>
								   	 	</el-col>
						  			</el-form-item>
        						</td>
        						<td class="secondtr" align="center" width="25%" height=20>
        							<input type="button" value="" class="del" @click="delatetr(index)"/>
        						</td>
        					</tr>
        		</table>
				
				<!---->
				<!---->
				
				<el-form-item label="" id="addPerson">
				    <el-button type="primary" style="float: left;" @click="addProduct"></el-button>
				</el-form-item>
				<!---->
				<!---->
				<table  id="insidedata2">
        					<tr >
        						<th class="firsttr" align="center" width="10%" height=20></th>
		                        <th class="firsttr" align="center" width="10%" height=20></th>
		                        <th class="firsttr" align="center" width="10%" height=20></th>
		                        <th class="firsttr" align="center" width="10%" height=20></th>
		                        <th class="firsttr" align="center" width="10%" height=20></th>
		                        <th class="firsttr" align="center" width="10%" height=20></th>
        					</tr>
        					<tr v-for="(item,index) in arrList2" >
        						<td class="secondtr" align="center" width="10%" height=20 >
        							<select name="" v-model="item.productCategory" placeholder="" style="width: 120px;">
        								<option :label="item.category" :value="item.id" v-for="item in productCategoryList"></option>
        							</select>	
        						</td>
        						<td class="secondtr" align="center" width="10%" height=20>
        								<input type="text"  class="inpu"v-model="item.unitPrice"/>
        						</td>
        						<td class="secondtr" align="center" width="10%" height=20>
        							<select name="" v-model="item.unit" placeholder="" style="width: 120px;">
        								<option  :label="item.category" :value="item.id" v-for="item in priceUnitCategoryList"></option>
        							</select>	
        						</td>
        						<td class="secondtr" align="center" width="10%" height=20>
        								<input type="text"  class="inpu" v-model="item.amount"/>
        						</td>
        						
        						<td class="secondtr" align="center" width="10%" height=20>
        								<input type="text"  class="inpu" v-model="item.summation"/>
        						</td>
        						<td class="secondtr" align="center" width="10%" height=20>
        							<input type="button" value="" class="del" @click="deletetrProduct(index)"/>
        						</td>
        					</tr>
        		</table>
				
			</div>		
				<el-form-item>
				    <el-button type="primary" @click="onSubmit"></el-button>
				    <el-button></el-button>
				</el-form-item>

		</el-form>
		<!---->
			<el-upload class="upload-demo" ref="upload"
		  action="https://jsonplaceholder.typicode.com/posts/"
		  :on-preview="handlePreview"
		  :on-remove="handleRemove"
		  :file-list="fileList"
		  :auto-upload="false">
		  <el-button slot="trigger" size="small" type="primary"></el-button>
		  <el-button style="margin-left: 10px;" size="small" type="success" @click="submitUpload"></el-button>
		</el-upload>
		
	</div>
</template>

<script>
	export default {
	    data() {
	      return {
	      	createDate:null,
	      	fileList: [],
	      	arrList:[{//
	        		paid:null,
	        		payTime:null,
	        }],
	        addcontractform: {},
	        areaCategoryList:[],
	        regionCategoryList:[],
	        invoiceCategoryList:[],
	        productCategoryList:[],//
	        priceUnitCategoryList:[],//
	        paymentList:[],//
	        contractProductList:[],//
	        arrList2:[{//
	        	amount:null,
	        	productCategory:null,
	        	summation:null,
	        	unit:null,
	        	unitPrice:null
	        }]
	      }
	    },
	    computed:{
	    	totalPrice(){ // 
	    		let total = null;
	    		this.arrList.map((item)=>{
	    			if(item.paid){
	    				total = total + item.paid*1
	    			}
	    			
	    		})
	    		return total;
	    	},
	    	totalAmount(){ //
	    		let total = null;
	    		this.arrList2.map(function(item){
	    			if(item.amount){
	    				total = total + item.amount*1;
	    			}
	    		})
	    		return total;
	    	},
	    	totalPrix(){ // 
	    		let total = null;
	    		this.arrList2.map(function(item){
	    			if(item.summation){
	    				total = total + item.summation*1;
	    			}
	    		})
	    		return total;
	    	}
	    },
	    mounted(){
	    	
	    },
	    methods: {
	      addfirst(){
	      	this.arrList.push({//
	    			paid:null,
	        		payTime:null,
	    		})
	      },
	      addProduct(){
	      	this.arrList2.push({
	      		amount:null,
	        	productCategory:null,
	        	summation:null,
	        	unit:null,
	        	unitPrice:null
	      	})
	      },
	      deletetrProduct(){
	      	if(index==0){//
    			this.arrList2[index].amount = null;
    			this.arrList2[index].productCategory = null;
    			this.arrList2[index].summation = null;
    			this.arrList2[index].unit = null;
    			this.arrList2[index].unitPrice = null;
    			return
    		}
    		this.arrList2.splice(index,1)//1
	      },
	      delatetr(index){
	      	if(index==0){//
	    			this.arrList[index].paid = null;
	    			this.arrList[index].payTime = null;
	    			return
	    		}
	    		this.arrList.splice(index,1)//1
	      },
	      onSubmit() {
	      	console.log(this.arrList)
	      	
	      	
	      	//
	      		for(var i = 0; i < this.arrList.length; i++){
	      			for(var key in this.arrList[i]){
	      				this.arrList[i].payTime = Date.parse(new Date(this.arrList[i].payTime))//
	      				if(this.arrList[i][key]){
	      					this.paymentList.push(this.arrList[i])
	      					break;
	      				}
	      			}
	      		}
	      	
	      	
	      	//
	      		for(var i = 0; i < this.arrList2.length; i++){
	      			for(var key in this.arrList2[i]){
	      				if(this.arrList2[i][key]){
	      					this.contractProductList.push(this.arrList2[i])
	      					break;
	      				}
	      			}
	      		}
	      	
	      	
			var that = this;
			that.addcontractform.id = this.uuid();//uuid
			
			that.addcontractform.createDate = that.createDate//
			that.addcontractform.paymentList = that.paymentList//
			that.addcontractform.contractProductList = that.contractProductList//
			 that.addcontractform.totalPrice=that.totalPrix //
			 that.addcontractform.paid=that.totalPrice //
			 that.addcontractform.totalQuantity=that.totalAmount //
			
			
	        this.$validator.validateAll().then(function(res){//
		    		if(res){
		    			console.log(res)
		    			that.addcontractform.depositBackTime = Date.parse(new Date(that.addcontractform.depositBackTime))//
						that.addcontractform.signDate = Date.parse(new Date(that.addcontractform.signDate))//
						that.addcontractform.preSignDate = Date.parse(new Date(that.addcontractform.preSignDate))//
		    			
		    			
		    			that.$http.ContractAddUrl(that.addcontractform).then(function(data){
							console.log(data)
							that.$router.push({path:'contractlist'});
						}).catch(function (error) {
						    console.log(error);
						});
		    		}
		    })
	      },
	      submitUpload() {
	        	this.$refs.upload.submit();
	      },
	      handleRemove(file, fileList) {
	        	console.log(file, fileList);
	      },
	      handlePreview(file) {
	        	console.log(file);
	      }
	    },
	    created(){
	    	var that = this;
	    	this.$http.allCategory().then(function(data){//
	    		console.log(data)
	    		that.areaCategoryList = data.data.areaCategoryList
	    		that.regionCategoryList = data.data.regionCategoryList
	    		that.invoiceCategoryList = data.data.invoiceCategoryList
	    		that.productCategoryList = data.data.productCategoryList;
	    		that.priceUnitCategoryList = data.data.priceUnitCategoryList;
	    	})
	    }
 	 }
</script>

<style>
	
	h5{
		color:#01a8a1;
		height: 20px;
	    border-bottom: 1px solid #01a8a1;
	    text-align: left;
	    line-height: 20px;
	}
	/**/
	#addcontractTab{
		/*width: 50%;*/
		margin: auto;
	}
	/**/
	.el-form-item__content .el-select--mini{
		float: left;
	}
	/*label*/
	.el-form-item__label{
		margin-top:4px;
	}
	/**/
	.el-form .el-form-item{
		margin: 0;
	}
	/**/
	.el-table__header,.el-table__body{
		margin: auto;
	}
	/**/
	/*area*/
	#addcontract .el-form .box .el-form-item--mini{
		width: 50%;
		margin: auto;
		margin:30px auto;
		
	}
	/*,*/
	#addcontract .el-form .box .el-checkbox-group label{
		float: left;
		width: 70px;
		margin-left: 0;
		text-align: left;		
	}
	#addcontract .el-form-item .el-form-item__label{
		width:130px!important;
	}
	#addcontract .el-form-item__content{
		margin-left: 130px!important;
	}
	/*textarea*/
	#addcontract .box textarea{
		height: 150px;
		margin-bottom: 18px;
	}
	#addcontract .text-box{
		max-width: 706px!important;
		margin: 0 auto;
	}
	
	/*label*/
	#addcontract .el-input__inner{
		width: 220px;
	}
	/*margin*/
	#addcontract .upload-demo{
		margin-top: 30px;
	}
	
	/**/
	#addcontract #insidedata{
		margin: auto;
		margin-top: 10px;
		margin-bottom: 10px;
		margin-left: 26px;
		width: 300px;
		border-collapse:collapse;
		border: 1px solid #ebeef5;
		font-size: 14px;
   		color: #606266;
	}
	#addcontract #insidedata .firsttr{
		background: #fff;
	}
	#addcontract #insidedata td{
		border: 1px solid #ebeef5;
	}
	#addcontract #insidedata .inpu{
		width: 210px;
		height: 23px;
		border-radius: 4px;
		border: 1px solid #D3DCE6;
	}
	#addcontract #insidedata .del{
		width: 50px;
		height: 23px;
		background: #01a8a1;
		border: none;
		color: #FFFFFF;
		border: 1px solid ##409EFF;
		font-size: 12px;
	}
	/*margin*/
	#insidedata .el-form-item__content{
		margin:0!important
	}
	/**/
	/*2*/
	#addcontract #insidedata2{
		margin: auto;
		margin-top: 10px;
		margin-bottom: 10px;
		margin-left: 26px;
		width: 100%;
		border-collapse:collapse;
		border: 1px solid #ebeef5;
		font-size: 14px;
   		color: #606266;
	}
	#addcontract #insidedata2 .firsttr{
		background: #fff;
	}
	#addcontract #insidedata2 td{
		border: 1px solid #ebeef5;
	}
	#addcontract #insidedata2 .inpu{
		width: 129px;
		border: 1px solid #D3DCE6;
	}
	#addcontract #insidedata2 .del{
		width: 50px;
		background: #01a8a1;
		border: none;
		color: #FFFFFF;
		font-size: 12px;
	}
	/*margin*/
	#insidedata2 .el-form-item__content{
		margin:0!important
	}
	/**/
	#addcontract .tipsinfo{
		font-size: 10px;
		color: red;
		position: absolute;
		top: 30px;
		left: 3px;
	}
	
	
	#addcontract .el-button--small{
		color:white !important;
	}
</style>


