<template>	
	<div id="">
		<h5></h5>
		<el-table :data="usermanageData" id="usermanageData" border >
			<el-table-column  prop="name" label="" width="200" height="30"></el-table-column>
			<el-table-column  prop="workplace" label="" width="180"></el-table-column>
			<el-table-column  prop="mobile" label=""  width="180"></el-table-column>
			<el-table-column  prop="email" label="" width="180"></el-table-column>
			<el-table-column  prop="createDate" label="" width="180"></el-table-column>
		    <el-table-column  label="" width="100" >
		      	<template slot-scope="scope">
		        	<el-button @click="handleClick(scope.row)" type="text" size="small"></el-button>
		      	</template>
		    </el-table-column>
		</el-table>
		<div class="block">
		    <el-pagination
		      @size-change="handleSizeChange"
		      @current-change="handleCurrentChange"
		      :current-page.sync="currentPage1"
		      :page-size="10"
		      layout="total, prev, pager, next"
		      :total="totalNum">
		    </el-pagination>
		</div>
	     <usermanageinfo v-if="usermanageinfo" :child-msg="id" v-on:usermanagelist="usermanagelist"></usermanageinfo>
  </div>
</template>

<script>
	import usermanageinfo from '@/components/usermanageinfo'//
export default {
	components:{
			usermanageinfo:usermanageinfo//
	},
	data(){
	  	return{
	  		usermanageinfo:false,
	  		id:null,
	  		totalNum:10,
	  		currentPage1: 1,
	        currentPage2: 5,
	        currentPage3: 5,
	        currentPage4: 4,
	        rows:10,
	        page:1,
	  		usermanageData: []
	  	}
	},
	created(){
		var that = this;
		
		
    	//formData
    	var fd = new FormData();
    	fd.append('rows',that.rows)
    	fd.append('page',that.page)
    	
		this.$http.UserListUrl(fd).then(function(data){
			console.log(data)
			var list = data.data.rows;
			for(var i = 0; i < list.length; i++){//XX-XX-XX
				list[i].createDate = that.NumConvertUtil.formatDate2(list[i].createDate)
			}
			that.usermanageData = list;//
			that.totalNum = data.data.total;//
		})
	},
	methods:{
		handleSizeChange(val) {
      	  console.log(` ${val} `);
      	},
      	handleCurrentChange(val) {//
        	console.log(`: ${val}`);
     	},
     	 handleClick(scope){
      		this.usermanageinfo = true;//
//  		console.log(scope)
    		this.id = scope.id;
      	},
      	usermanagelist(res){
    		this.usermanageinfo = res//
    	}
	}
	
}
</script>
<style>
	/*table*/
	#usermanageData .el-table__header-wrapper{
		line-height: 0px;
	}
	/**/
	#usermanageData .cell{
		text-align: center;
	}
	/*label*/
	#usermanageData .el-form-item__label{
		margin-top:4px;
	}
	#usermanageData .el-form-item__label:nth-of-type(6){
		margin-top:none;
	}
	/**/
	#usermanageData .el-form .el-form-item{
		margin: 0;
	}
	/**/
	#usermanageData .el-table{
		background: none;
	}
	/**/
	#usermanageData .el-table__header,.el-table__body{
		margin: auto;
	}
	/**/
	#usermanageData .el-form-item--mini{
		/*width: 80%;*/
		margin: auto;
	}
	
	#usermanageData{
		max-width: 1021px;
		margin: 0 auto;
	}
	
</style>


