<template>
	<div id="markettracking">
		<h5></h5>
		<el-form ref="form" :model="marketForm" label-width="80px" size="mini">
			<table id="marketFormtopTab">
				<!---->
				<tr>
					<td>
						<el-form-item label="">
				  			<el-input  v-model="marketForm.surveyObject"></el-input>
				  		</el-form-item>
					</td>
					<td>
						<el-form-item label="" >
				  			<el-input v-model="marketForm.responsiblePerson"></el-input>
				  		</el-form-item>
					</td>
				</tr>
				<!---->
				<tr>
					<td>
						<el-form-item label="">
					    	<el-select v-model="marketForm.workCategory" placeholder="">
					      		<el-option label="" value=""></el-option>
					      		<el-option :label="item.category" :value="item.id" v-for="item in workCategoryList"></el-option>
					    	</el-select>
					  	</el-form-item>
					</td>
					<td>
						<el-form-item label="">
				    		<el-select v-model="marketForm.feedbackCategory" placeholder="">
				    			<el-option label="" value=""></el-option>
				      			<el-option :label="item.category" :value="item.id" v-for="item in feedbackCategoryList"></el-option>
				    		</el-select>
				  		</el-form-item>
					</td>
				</tr>
				<!---->
				<tr>
					<td>
						<el-form-item label="">
			    			<el-select v-model="marketForm.area" placeholder="">
			    				<el-option label="" value=""></el-option>
			      				<el-option :label="item.category" :value="item.id" v-for="item in areaCategoryList"></el-option>
			    			</el-select>
			  			</el-form-item>
					</td>
					<td>
						<el-form-item label="">
			    			<el-select v-model="marketForm.region" placeholder="">
			    				<el-option label="" value=""></el-option>
			      				<el-option :label="item.category" :value="item.id" v-for="item in regionCategoryList"></el-option>
			   				</el-select>
			  			</el-form-item>
					</td>
				</tr>
				<!---->
				<tr>
					<td>
						<el-form-item label="">
					    	<el-col :span="11">
					    		<el-date-picker type="date" placeholder="" v-model="marketForm.surveyStartTime" style="width: 100%;"></el-date-picker>
					   	 	</el-col>
					    	<el-col class="line" :span="1">-</el-col>
					    	<el-col :span="11">
					    		<el-date-picker type="date" placeholder="" v-model="marketForm.surveyEndTime" style="width: 100%;"></el-date-picker>
					    	</el-col>
						</el-form-item>
					</td>
					<td>
						
					</td>
				</tr>
				<!---->
				<tr>
					<td>
						<el-form-item size="large">
						    <el-button type="primary" @click="onSubmit"></el-button>
						</el-form-item>
					</td>
				</tr>
			</table>
			<!---->
		</el-form>
	<!---->
	
		<el-table :data="markettableData" id="markettableData" border >
			<el-table-column  prop="surveyObject" label="" width="190" height="30"></el-table-column>
			<el-table-column  prop="responsiblePerson" label="" width="150"></el-table-column>
			<el-table-column  prop="surveyStartTime" label="" sortable width="150"></el-table-column>
			<el-table-column  prop="workCategoryModel.category" label="" width="150"></el-table-column>
			<el-table-column  prop="feedbackCategoryModel.category" label="" width="80"></el-table-column>
			<el-table-column  prop="areaCategory.category" label="" width="80"></el-table-column>
			<el-table-column  prop="regionCategory.category" label="" width="80"></el-table-column>
		    <el-table-column  label="" width="100">
		      	<template slot-scope="scope">
		        	<el-button @click="handleClick(scope.row)" type="text" size="small" ></el-button>
		        	<el-button @click="edit(scope.row)" type="text" size="small"></el-button>
		      	</template>
		    </el-table-column>
		</el-table>
		<!---->
		<div class="block">
	    <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page.sync="currentPage1"
	      :page-size="10"
	      layout="total, prev, pager, next"
	      :total="totalNum">
	    </el-pagination>
	  </div>
	  <!---->
	  <marketedit v-if="marketedit" v-on:marketedittwo="marketedittwo" :child-msg="id"></marketedit>
	  <marketsee v-if="marketsee" v-on:marketseetwo="marketseetwo" :child-msg="id"></marketsee>
	  
	</div>
</template>

<script>
	import Marketedit from '@/components/Marketedit'//
	import Marketsee from '@/components/Marketsee'//
	export default {
	components:{
			marketedit:Marketedit,//
			marketsee:Marketsee//
	},
    data() {
      return {
      	id:null,
      	marketedit:false,
      	marketsee:false,
      	currentPage1: 1,
        currentPage2: 5,
        currentPage3: 5,
        currentPage4: 4,
        marketForm: {//v-model
        	surveyObject:'',
        	responsiblePerson:'',
        	workCategory:'',
        	feedbackCategory:'',
        	area:'',
        	region:'',
        	surveyStartTime:'',
        	surveyEndTime:'',
        	sort:"surveyStartTime",
        	order:"desc",
        	page:1,
        	rows:10
        },
        inputMarketForm: {//
        	surveyObject:'',
        	responsiblePerson:'',
        	workCategory:'',
        	feedbackCategory:'',
        	area:'',
        	region:'',
        	surveyStartTime:'',
        	surveyEndTime:'',
        	sort:"surveyStartTime",
        	order:"desc",
        	page:1,
        	rows:10
        },
        totalNum:0,
        workCategoryList:[],
        feedbackCategoryList:[],
        areaCategoryList:[],
        regionCategoryList:[],
        markettableData: [{
        }]
      };
    },
    created(){
    	//
    	var that = this;
    	this.$http.getMarketTypeinfo().then(function(data){
//  		console.log(data)
    		that.workCategoryList     = data.data.workCategoryList//
    		that.feedbackCategoryList = data.data.feedbackCategoryList//
    		that.areaCategoryList     = data.data.areaCategoryList//
    		that.regionCategoryList   = data.data.regionCategoryList//
    	})
    	
    	//formData
    	var fd = new FormData();
    	fd.append("surveyObject",this.inputMarketForm.surveyObject)
    	fd.append("responsiblePerson",this.inputMarketForm.responsiblePerson)
    	fd.append("workCategory",this.inputMarketForm.workCategory)
    	fd.append("feedbackCategory",this.inputMarketForm.feedbackCategory)
    	fd.append("area",this.inputMarketForm.area)
    	fd.append("region",this.inputMarketForm.region)
    	fd.append("surveyStartTime",this.inputMarketForm.surveyStartTime)
    	fd.append("surveyEndTime",this.inputMarketForm.surveyEndTime)
    	fd.append("sort",this.inputMarketForm.sort)
    	fd.append("order",this.inputMarketForm.order)
    	fd.append("page",this.inputMarketForm.page)
    	fd.append("rows",this.inputMarketForm.rows)
    	
    	//
    	var that = this;//this
    	this.$http.getMarketList(fd).then(function(data){//fddata
	  		that.totalNum = data.data.total;//
	  		var list = data.data.rows;//
	  		console.log(data)
	  		for(var i = 0; i < list.length; i++){//XX-XX-XX
				list[i].surveyStartTime = that.NumConvertUtil.formatDate2(list[i].surveyStartTime)
			}
			that.markettableData = list;//
    	})

    },
    methods: {
    	edit(scope){
//  		console.log(scope.id)
    		this.marketedit = true;//
    		this.id = scope.id;
    	},
    	handleClick(scope){
    		this.marketsee = true;//
//  		console.log(scope)
    		this.id = scope.id;
    		
    	},
    	marketseetwo(res){
    		this.marketsee = res//
    	},
    	marketedittwo(res){
    		this.marketedit = res//
    	},
    	formatDate(date){//
      	if(!date){
      		return 
      	}
      		var date = new Date(date);
	      	var year = date.getFullYear();
	      	var month = date.getMonth();
	      	var day = date.getDate();
	      	return year + '-' + ((month+1)<10?'0'+(month+1):(month+1)) + '-' +(day<10?'0'+day:day)
	  
	//    	consolconsolee.log(date.getFullYear())
      },
      onSubmit() {
      	this.currentPage1 = 1;
      	this.inputMarketForm = JSON.parse(JSON.stringify(this.marketForm));//
	        var fd = new FormData();
	    	fd.append("surveyObject",this.inputMarketForm.surveyObject)
	    	fd.append("responsiblePerson",this.inputMarketForm.responsiblePerson)
	    	fd.append("workCategory",this.inputMarketForm.workCategory)
	    	fd.append("feedbackCategory",this.inputMarketForm.feedbackCategory)
	    	fd.append("area",this.inputMarketForm.area)
	    	fd.append("region",this.inputMarketForm.region)
	    	fd.append("surveyStartTime",this.formatDate(this.inputMarketForm.surveyStartTime))//
	    	fd.append("surveyEndTime",this.formatDate(this.inputMarketForm.surveyEndTime))
	    	fd.append("sort",this.inputMarketForm.sort)
	    	fd.append("order",this.inputMarketForm.order)
	    	fd.append("page",1)
	    	fd.append("rows",this.inputMarketForm.rows)
	    	
	    	//
	    	var that = this;//this
	    	this.$http.getMarketList(fd).then(function(data){//fddata
		  		that.totalNum = data.data.total;//
		  		var list = data.data.rows;//
	//	  		console.log(list)
		  		for(var i = 0; i < list.length; i++){//XX-XX-XX
					list[i].surveyStartTime = that.NumConvertUtil.formatDate2(list[i].surveyStartTime)
				}
				that.markettableData = list;//
	    	})
      },
      handleSizeChange(val) {
        console.log(` ${val} `);
      },
      handleCurrentChange(val) {//
        console.log(`: ${val}`);
//      this.inputMarketForm =null

        var fd = new FormData();
    	fd.append("surveyObject",this.inputMarketForm.surveyObject)
    	fd.append("responsiblePerson",this.inputMarketForm.responsiblePerson)
    	fd.append("workCategory",this.inputMarketForm.workCategory)
    	fd.append("feedbackCategory",this.inputMarketForm.feedbackCategory)
    	fd.append("area",this.inputMarketForm.area)
    	fd.append("region",this.inputMarketForm.region)
    	fd.append("surveyStartTime",this.formatDate(this.inputMarketForm.surveyStartTime))//
    	fd.append("surveyEndTime",this.formatDate(this.inputMarketForm.surveyEndTime))
    	fd.append("sort",this.inputMarketForm.sort)
    	fd.append("order",this.inputMarketForm.order)
    	fd.append("page",val)
    	fd.append("rows",this.inputMarketForm.rows)
    	//
    	var that = this;
    	this.$http.getMarketList(fd).then(function(data){
	  		that.totalNum = data.data.total;
	  		var list = data.data.rows;
//	  		console.log(list)
	  		for(var i = 0; i < list.length; i++){//XX-XX-XX
				list[i].surveyStartTime = that.NumConvertUtil.formatDate2(list[i].surveyStartTime)
			}
			that.markettableData = list;
    	})
      }
    }
  };
	
</script>

<style>
	/**/
	.el-form-item__content .el-select--mini{
		float: left;
	}
	/**/
	.el-button--large{
		float: left;
		margin:10px 0;
	}
	/*table*/
	.el-table__header-wrapper{
		line-height: 0px;
	}
	/**/
	.cell{
		text-align: center;
	}
	/*label*/
	.el-form-item__label{
		margin-top:4px;
	}
	.el-form-item__label:nth-of-type(6){
		margin-top:none;
	}
	/**/
	.el-form .el-form-item{
		margin: 0;
	}
	/**/
	.el-table{
		background: none;
	}
	/**/
	.el-table__header,.el-table__body{
		margin: auto;
	}
	/**/
	#marketFormtopTab{
		/*max-width: 786px;*/
		margin: auto;
	}
	.el-form-item--mini{
		/*width: 80%;*/
		margin: auto;
	}
	
	#markettableData{
		max-width: 1106px;
		margin: 0 auto;
	}
	#markettracking h5{
		color:#01a8a1;
		height: 20px;
	    border-bottom: 1px solid #01a8a1;
	    text-align: left;
	    line-height: 20px;
	}
	
</style>


