<template>
	<div>
		<div id="echartsBiddinginfo">
	    <!--echarts-->
	   		<div id="biddingNum" style="width:500px; height:500px"></div>
	   		<div id="biddingMoney" style="width:500px; height:500px"></div>
	   		<div id="tenderWeight" style="width:500px; height:500px"></div>
	   		<div id="bidWinTotalWeight" style="width:500px; height:500px"></div>
	   		<div id="bidWinWeight" style="width:900px; height:900px"></div>
	   		<!--<div id="biddingProportion" style="width:500px; height:500px"></div>-->
	   	</div> 
	</div>
</template>

<script>
//	var echarts = require('echarts');//echarts
	export default{
		name:'marketInformation',
		mounted() {
			//strat
			var biddingNum = this.echarts.init(document.getElementById('biddingNum'));//
			var biddingMoney = this.echarts.init(document.getElementById('biddingMoney'));//
			var tenderWeight = this.echarts.init(document.getElementById('tenderWeight'));//
			var bidWinTotalWeight = this.echarts.init(document.getElementById('bidWinTotalWeight'));//
			var bidWinWeight = this.echarts.init(document.getElementById('bidWinWeight'));//
			
			let that = this;
			
			/**/
			biddingNum.setOption({
                title: {
                    show:true,
                    x:'center',
                    text: ''
                },
                tooltip: {},//
                legend: {
                    y:'bottom',
					data:['','',''],
                },
                xAxis: {
                    data: [],
                },
                yAxis: {

                    type : 'value',
                    axisLabel:{formatter:'{value} '}

                },

                series: [
                    {
                        barGap:'0%',
                        name: '',
                        type: 'bar',
                        data: [],
                        itemStyle:{
                            normal:{
                                label:{
                                    show:true,
                                    position:'top',
                                    textStyle:{
                                        fontSize : '15',
                                        fontFamily : '',
                                    }
                                }
                            }
                        },
                    },
                    {
                        barGap:'0%',
                        name: '',
                        type: 'bar',
                        data: [],
                        itemStyle:{
                            normal:{
                                label:{
                                    show:true,
                                    position:'top',
                                    textStyle:{
                                        fontSize : '15',
                                        fontFamily : '',
                                    }
                                }
                            }
                        },
                    },
                    {
                        name: '',
                        type: 'bar',
                        data: [],
                        itemStyle:{
                            normal:{
                                label:{
                                    show:true,
                                    position:'top',
                                    textStyle:{
                                        fontSize : '15',
                                        fontFamily : '',
                                    }
                                }
                            }
                        },
                    }

                ]

            });

            biddingNum.showLoading();
            this.$http.biddingNum().then(function(res){
            	console.log(res)
                biddingNum.hideLoading();
                biddingNum.setOption({
                    xAxis: {
                        data: res.data.title,
                    },
                    series: [
                        {
                            name: '',
                            data: res.data.p1,
                        },
                        {
                            name: '',
                            data: res.data.p2,
                        },
                        {
                            name: '',
                            data: res.data.p3,
                        }
                    ]
                });
            });
			
			
			
			
			//
			 biddingMoney.setOption({
                title: {
                    show:true,
                    x:'center',
                    text: ''
                },
                tooltip: {},//
                legend: {
                    y:'bottom',
                    data:['','',''],

                },
                xAxis: {
                    data: [],
                },
                yAxis: {

                    type : 'value',
                    axisLabel:{formatter:'{value} '}

                },

                series: [
                    {
                        barGap:'0%',
                        name: '',
                        type: 'bar',
                        data: [],
                        itemStyle:{
                            normal:{
                                label:{
                                    show:true,
                                    position:'top',
                                    textStyle:{
                                        fontSize : '15',
                                        fontFamily : '',
                                    }
                                }
                            }
                        },
                    },
                    {
                        name: '',
                        type: 'bar',
                        data: [],
                        itemStyle:{
                            normal:{
                                label:{
                                    show:true,
                                    position:'top',
                                    textStyle:{
                                        fontSize : '15',
                                        fontFamily : '',
                                    }
                                }
                            }
                        },
                    },
                    {
                        name: '',
                        type: 'bar',
                        data: [],
                        itemStyle:{
                            normal:{
                                label:{
                                    show:true,
                                    position:'top',
                                    textStyle:{
                                        fontSize : '15',
                                        fontFamily : '',
                                    }
                                }
                            }
                        },
                    }

                ]

            });

            biddingMoney.showLoading();
            this.$http.biddingMoney().then(function(res){
                biddingMoney.hideLoading();
                biddingMoney.setOption({
                    xAxis: {
                        data: res.data.title,
                    },
                    series: [
                        {
                            name: '',
                            data: res.data.p1,
                        },
                        {
                            name: '',
                            data: res.data.p2,
                        },
                        {
                            name: '',
                            data: res.data.p3,
                        }
                    ]
                });
            });
			
			
			//
			
            tenderWeight.setOption({
                title: {
                    show:true,
                    x:'center',
                    text: ''
                },
                tooltip: {},//
                legend: {
                    y:'bottom',
                    data:[],

                },

                series: [
                    {
                        name:'',
                        type:'pie',
                        radius: ['30%', '40%'],
                        legendHoverLink: false, //
                        hoverAnimation:false,//
                        label: {
                            show:true,
                            position:'outside',

                        },
                        labelLine: {
                            show:true,
                            length:50,

                        },
                        data:[],
                        itemStyle:{
                            normal:{
                                label:{
                                    show: true,
                                    formatter:function(data){
                                    	return that.NumConvertUtil.getNumConvertMoney(data.value)+"("+(data.percent)+"%)";
//                                      return data.name+":" +NumConvertUtil.getShortMoney(data.value) +"("+(data.percent)+"%)";
                                    }
                                },
                                labelLine :{show:true}
                            }
                        }
                    }

                ]

            });


            tenderWeight.showLoading();
            this.$http.bidCharTenderWeight().then(function(res){
                tenderWeight.hideLoading();

                tenderWeight.setOption({
                    legend: {
                        data: res.data.category
                    },
                    series: [
                        {
                            name: '',
                            data: res.data.pie,
                        }
                    ]
                });
            });
			//
			//
			
			
			
			
			//
            bidWinTotalWeight.setOption({
                title: {
                    show:true,
                    x:'center',
                    text: ''
                },
                tooltip: {},//
                legend: {
                    y:'bottom',
                    data:[],

                },

                series: [
                    {
                        name:'',
                        type:'pie',
                        radius: ['30%', '40%'],
                        legendHoverLink: false, //
                        hoverAnimation:false,//
                        label: {
                            show:true,
                            position:'outside',

                        },
                        labelLine: {
                            show:true,
                            length:50,

                        },
                        data:[],
                        itemStyle:{
                            normal:{
                                label:{
                                    show: true,
                                    formatter:function(data){
                                        return that.NumConvertUtil.getNumConvertMoney(data.value) +"("+(data.percent)+"%)";
                                    }
                                },
                                labelLine :{show:true}
                            }
                        }
                    }

                ]

            });


            bidWinTotalWeight.showLoading();
            this.$http.bidChartWinTotalWeight().then(function(res){
                bidWinTotalWeight.hideLoading();

                bidWinTotalWeight.setOption({
                    legend: {
                        data: res.data.category
                    },
                    series: [
                        {
                            name: '',
                            data: res.data.pie,
                        }
                    ]
                });
            });
			
			
			
			
			// 
			

            bidWinWeight.setOption({
                title: {
                    show:true,
                    x:'center',
                    text: ''
                },
                legend: {
                    y:'bottom',
                    data:[],

                },
				tooltip: {},//
                series: [
                    {
                        name:'',
                        type:'pie',
                        radius: ['30%', '40%'],
                        legendHoverLink: false, //
                        hoverAnimation:false,//
                        label: {
                            show:true,
                            position:'outside',

                        },
                        labelLine: {
                            show:true,
                            length:50,

                        },
                        data:[],
                        itemStyle:{
                            normal:{
                                label:{
                                    show: true,
                                    formatter:function(data){
//                                  	 return that.NumConvertUtil.getNumConvertMoney(data.value) +"("+(data.percent)+"%)";
                                        return data.name+":" + that.NumConvertUtil.getShortMoney(data.value) +"("+(data.percent)+"%)";
                                    }
                                },
                                labelLine :{show:true}
                            }
                        }
                    }

                ]

            });


            bidWinWeight.showLoading();
            this.$http.bidChartWinWeight().then(function(res){
                bidWinWeight.hideLoading();
				console.log(res)
                bidWinWeight.setOption({
                    legend: {
                        data: res.data.category
                    },
                    series: [
                        {
                            name: '',
                            data: res.data.pie,
                        }
                    ]
                });
            });

			
			//end
		}
	}
</script>

<style>
#echartsBiddinginfo{
		max-width:1100px ;
		height: 100%;
		margin: auto;
		/*border:1px solid #3762BC*/
	}
	#biddingNum,#biddingMoney,#biddingProportion,#tenderWeight,#bidWinTotalWeight,#bidWinWeight{
		margin: 20px;
		display: inline-block;
		/*float: left;*/
/*		border:1px solid red;*/
	}
</style>


