<template>
	<div id="biddinglist">
		<h5></h5>
		<el-form ref="form" :model="biddinglistForm" label-width="80px" size="mini">
			<table id="biddinglistFormtopTab">
				<!---->
				<tr>
					<td>
						<el-form-item label="" >
				  			<el-input v-model="biddinglistForm.bidCompany"></el-input>
				  		</el-form-item>
					</td>
					<td>
						<el-form-item label="">
			    			<el-select v-model="biddinglistForm.status" placeholder="">
			      				<el-option label="" value=""></el-option>
				      			<el-option :label="item.category" :value="item.id" v-for="item in auditlist"></el-option>
			   				</el-select>
			  			</el-form-item>
					</td>
				</tr>
				<!---->
				<tr>
					<td>
						<el-form-item label="">
					    	<el-col :span="11">
					    		<el-date-picker type="date" placeholder="" v-model="biddinglistForm.bidStartDate" ></el-date-picker>
					   	 	</el-col>
						</el-form-item>
					</td>
					<td>
						<el-form-item label="">
					    	<el-col :span="11">
					    		<el-date-picker type="date" placeholder="" v-model="biddinglistForm.bidEndDate" ></el-date-picker>
					    	</el-col>
						</el-form-item>
					</td>
				</tr>
				<!---->
				<tr>
					<td>
						<el-form-item label="">
			    			<el-select v-model="biddinglistForm.area" placeholder="">
			      				<el-option label="" value=""></el-option>
				      			<el-option :label="item.category" :value="item.id" v-for="item in areaCategoryList"></el-option>
			    			</el-select>
			  			</el-form-item>
					</td>
					<td>
						<el-form-item label="">
			    			<el-select v-model="biddinglistForm.region" placeholder="">
			      				<el-option label="" value=""></el-option>
				      			<el-option :label="item.category" :value="item.id" v-for="item in regionCategoryList"></el-option>
			   				</el-select>
			  			</el-form-item>
					</td>
				</tr>
				<!---->
				<tr>
					<td>
						<el-form-item size="large">
						    <el-button type="primary" @click="onSubmit"></el-button>
						</el-form-item>
					</td>
				</tr>
			</table>
			<!---->
		</el-form>
	<!---->
	
		<el-table :data="biddinglisttableData" border style="width: 100%">
			<el-table-column  prop="bidCompany" label="" width="150" height="30"></el-table-column>
			<el-table-column  prop="bidProject" label="" width="150"></el-table-column>
			<el-table-column  prop="bidTime" label="" sortable width="150"></el-table-column>
			<el-table-column  prop="bidAuditCategory.category" label="" width="150"></el-table-column>
			<el-table-column  prop="areaCategory.category" label="" width="150"></el-table-column>
			<el-table-column  prop="regionCategory.category" label="" width="150"></el-table-column>
		    <el-table-column  label="" width="100">
		      	<template slot-scope="scope">
		        	<el-button @click="handleClick(scope.row)" type="text" size="small"></el-button>
		        	<el-button type="text"@click="handleClick2(scope.row)" size="small"></el-button>
		      	</template>
		    </el-table-column>
		</el-table>
		
		<div class="block">
	    <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page.sync="currentPage1"
	      :page-size="10"
	      layout="total, prev, pager, next"
	      :total="totalNum">
	    </el-pagination>
	  </div>
		
		<biddingsee v-if="biddingsee" v-on:bidding="bidding" :child-msg="id"></biddingsee>
		<biddingedit v-if="biddingedit" v-on:biddingeditt="biddingeditt" :child-msg="id"></biddingedit>
		
	</div>
</template>

<script>
	import biddingsee from '@/components/biddingsee'//
	import biddingedit from '@/components/biddingedit'//
	export default {
	components:{
		biddingsee:biddingsee,
		biddingedit:biddingedit
	},
    data() {
      return {
      	id:null,
      	biddingedit:false,
      	biddingsee:false,
      	totalNum:0,
      	currentPage1: 1,
        currentPage2: 5,
        currentPage3: 5,
        currentPage4: 4,
        biddinglistForm: {
          	bidCompany:'',
        	bider:'',
        	bidStartDate:'',
        	bidEndDate:'',
        	status:'',
        	area:'',
        	region:'',
        	sort:'bidTime',
        	order:'desc',
        	page:1,
        	rows:10
        },
        inputbiddingform:{//
        	bidCompany:'',
        	bider:'',
        	bidStartDate:'',
        	bidEndDate:'',
        	status:'',
        	area:'',
        	region:'',
        	sort:'bidTime',
        	order:'desc',
        	page:1,
        	rows:10
        },
        biddinglisttableData: [],
        areaCategoryList:[],
        invoiceCategoryList:[],
        productCategoryList:[],
        regionCategoryList:[],
        auditlist:[]
      };
    },
    methods: {
    	handleClick(scope){//
    		this.biddingsee = true
    		this.id = scope.id;
//  		console.log(scope.id)
    	},
    	handleClick2(scope){//
    		this.biddingedit = true
    		this.id = scope.id;
    	},
    	bidding(res){
    		this.biddingsee = res// = res//
    	},
    	biddingeditt(res){
    		this.biddingedit = res// = res//
    	},
    	formatDate(date){//
      	if(!date){
      		return 
      	}
      		var date = new Date(date);
	      	var year = date.getFullYear();
	      	var month = date.getMonth();
	      	var day = date.getDate();
	      	return year + '-' + ((month+1)<10?'0'+(month+1):(month+1)) + '-' +(day<10?'0'+day:day)
	  
	//    	console.log(date.getFullYear())
      },
    	onSubmit() {
        	console.log('submit!');
        	this.currentPage1 = 1;
      		this.inputbiddingform = JSON.parse(JSON.stringify(this.biddinglistForm));//
        	var fd = new FormData();
	    	fd.append("bidCompany",this.inputbiddingform.bidCompany)
	    	fd.append("bider",this.inputbiddingform.bider)
	    	fd.append("bidStartDate",this.formatDate(this.inputbiddingform.bidStartDate))//
	    	fd.append("bidEndDate",this.formatDate(this.inputbiddingform.bidEndDate))
	    	fd.append("status",this.inputbiddingform.status)
	    	fd.append("area",this.inputbiddingform.area)
	    	fd.append("region",this.inputbiddingform.region)
	    	fd.append("sort",this.inputbiddingform.sort)
	    	fd.append("order",this.inputbiddingform.order)
	    	fd.append("page",1)
	    	fd.append("rows",this.inputbiddingform.rows)
	    	
	    	//
	    	var that = this;//this
	    	this.$http.bidListUrl(fd).then(function(data){
	    		console.log(data)
	    		that.totalNum = data.data.total;//
	    		var list = data.data.rows;//
	    		for(var i = 0; i < list.length; i++){//XX-XX-XX
					list[i].bidTime = that.NumConvertUtil.formatDate2(list[i].bidTime)
				}
				that.biddinglisttableData = list;//
	    	})
      	},
        handleSizeChange(val) {
        	console.log(` ${val} `);
      	},
      	handleCurrentChange(val) {//
        	console.log(`: ${val}`);
        	//formData
	    	var fd = new FormData();
	    	fd.append("bidCompany",this.inputbiddingform.bidCompany)
	    	fd.append("bider",this.inputbiddingform.bider)
	    	fd.append("bidStartDate",this.inputbiddingform.bidStartDate)
	    	fd.append("bidEndDate",this.inputbiddingform.bidEndDate)
	    	fd.append("status",this.inputbiddingform.status)
	    	fd.append("area",this.inputbiddingform.area)
	    	fd.append("region",this.inputbiddingform.region)
	    	fd.append("sort",this.inputbiddingform.sort)
	    	fd.append("order",this.inputbiddingform.order)
	    	fd.append("page",val)
	    	fd.append("rows",this.inputbiddingform.rows)
	    	
	    	//
	    	var that = this;//this
	    	this.$http.bidListUrl(fd).then(function(data){
	    		console.log(data)
	    		that.totalNum = data.data.total;//
	    		var list = data.data.rows;//
	    		for(var i = 0; i < list.length; i++){//XX-XX-XX
					list[i].bidTime = that.NumConvertUtil.formatDate2(list[i].bidTime)
				}
				that.biddinglisttableData = list;//
	    	})
        	
	  	}
    },
    created(){
    	var that = this;
    	this.$http.contractSelectCategory().then(function(data){
    		that.areaCategoryList    = data.data.areaCategoryList
    		that.invoiceCategoryList = data.data.invoiceCategoryList
    		that.productCategoryList = data.data.productCategoryList
    		that.regionCategoryList  = data.data.regionCategoryList
    	})
    	
    	this.$http.bidAuditCategory().then(function(data){
    			that.auditlist =data.data
    	})
    	
    	
    	
    	
    	
    	
    	
    	
    	
    	
    	
    	
    	
    	
    	
    	
    	
    	
    	//formData
    	var fd = new FormData();
    	fd.append("bidCompany",this.inputbiddingform.bidCompany)
    	fd.append("bider",this.inputbiddingform.bider)
    	fd.append("bidStartDate",this.inputbiddingform.bidStartDate)
    	fd.append("bidEndDate",this.inputbiddingform.bidEndDate)
    	fd.append("status",this.inputbiddingform.status)
    	fd.append("area",this.inputbiddingform.area)
    	fd.append("region",this.inputbiddingform.region)
    	fd.append("sort",this.inputbiddingform.sort)
    	fd.append("order",this.inputbiddingform.order)
    	fd.append("page",this.inputbiddingform.page)
    	fd.append("rows",this.inputbiddingform.rows)
    	
    	//
    	var that = this;//this
    	this.$http.bidListUrl(fd).then(function(data){
    		console.log(data)
    		that.totalNum = data.data.total;//
    		var list = data.data.rows;//
    		for(var i = 0; i < list.length; i++){//XX-XX-XX
				list[i].bidTime = that.NumConvertUtil.formatDate2(list[i].bidTime)
			}
			that.biddinglisttableData = list;//
    	})
    }
  };
	
</script>

<style>
	/**/
	.el-form-item__content .el-select--mini{
		float: left;
	}
	/**/
	.el-button--large{
		float: left;
		margin:10px 0;
	}
	/*table*/
	.el-table__header-wrapper{
		line-height: 0px;
	}
	/**/
	.cell{
		text-align: center;
	}
	/*label*/
	.el-form-item__label{
		margin-top:4px;
	}
	.el-form-item__label:nth-of-type(6){
		margin-top:none;
	}
	/**/
	.el-form .el-form-item{
		margin: 0;
	}
	/**/
	.el-table{
		background: none;
	}
	/**/
	.el-table__header,.el-table__body{
		margin: auto;
	}
	/**/
	#biddinglistFormtopTab{
		/*width: 50%;*/
		margin: auto;
	}
	.el-form-item--mini{
		/*width: 80%;*/
		margin: auto;
	}
	
		/*label*/
	#biddinglistFormtopTab .el-form-item .el-form-item__label{
		width:110px!important;
	}
	#biddinglistFormtopTab .el-form-item__content{
		margin-left: 110px!important;
	}
	/**/
	#biddinglistFormtopTab .el-input__inner{
		width:219px
	}
	
	#biddinglist h5{
		color:#01a8a1;
		height: 20px;
	    border-bottom: 1px solid #01a8a1;
	    text-align: left;
	    line-height: 20px;
	}
	
</style>


