<template>
	<div id="trackmarketinfo">
		<h5></h5>
		<el-form ref="form" :model="trackform" label-width="80px" size="mini">
			<table id="trackformTab">
				<!---->
				<tr>
					<td>
						<el-form-item label="" >
				  			<el-input v-model="trackform.surveyObject" v-validate="'required'" name="name1"></el-input>
				  			<span class="tipsinfo" v-show="errors.has('name1')"></span>
				  		</el-form-item>
					</td>
					<td>
						<el-form-item label="">
				  			<el-input v-model="trackform.responsiblePerson" v-validate="'required'" name="name2"></el-input>
				  			<span class="tipsinfo" v-show="errors.has('name2')"></span>
				  		</el-form-item>
					</td>
				</tr>
				<!---->
				<tr>
					<td>
						<el-form-item label="">
					    	<el-select v-model="trackform.workCategory" placeholder="" style="width:220px"  v-validate="'required'" name="name3">
					      		<el-option label="" value=""></el-option>
					      		<el-option :label="item.category" :value="item.id" v-for="item in workCategoryList"></el-option>
					    	</el-select>
					    	<span class="tipsinfo" v-show="errors.has('name3')"></span>
					  	</el-form-item>
					</td>
					<td>
						<el-form-item label="">
				    		<el-select v-model="trackform.feedbackCategory" placeholder=""  v-validate="'required'" name="name4">
				      			<el-option label="" value=""></el-option>
				      			<el-option :label="item.category" :value="item.id" v-for="item in feedbackCategoryList"></el-option>
				    		</el-select>
				    		<span class="tipsinfo" v-show="errors.has('name4')"></span>
				  		</el-form-item>
					</td>
				</tr>
				<!---->
				<tr>
					<td>
						<el-form-item label="">
			    			<el-select v-model="trackform.area" placeholder="" style="width:220px"  v-validate="'required'" name="name5">
			      				<el-option label="" value=""></el-option>
			      				<el-option :label="item.category" :value="item.id" v-for="item in areaCategoryList"></el-option>
			    			</el-select>
			    			<span class="tipsinfo" v-show="errors.has('name5')"></span>
			  			</el-form-item>
					</td>
					<td>
						<el-form-item label="">
			    			<el-select v-model="trackform.region" placeholder=""  v-validate="'required'" name="name6">
			      				<el-option label="" value=""></el-option>
			      				<el-option :label="item.category" :value="item.id" v-for="item in regionCategoryList"></el-option>
			   				</el-select>
			   				<span class="tipsinfo" v-show="errors.has('name6')"></span>
			  			</el-form-item>
					</td>
				</tr>
				<!---->
				<tr>
					<td>
						<el-form-item label="">
					    	<el-col :span="11" >
					    		<el-date-picker type="date" placeholder="" v-model="trackform.surveyStartTime" ></el-date-picker>
					   	 	</el-col>
					   	 	<!--<span class="tipsinfo" v-show="errors.has('name7')"></span>-->
						</el-form-item>
					</td>
					<td>
						<el-form-item label="" >
				  			<el-input v-model="trackform.surveyDays" v-validate="'required'" name="name8"></el-input>
				  			<span class="tipsinfo" v-show="errors.has('name8')"></span>
				  		</el-form-item>
					</td>
				</tr>
				
			</table>
			
			<div class="text-box">
				<!---->
				<el-form-item label="" class="box">
				    <el-input type="textarea"  v-model="trackform.workContent" v-validate="'required'" name="name9"></el-input>
				    <span class="tipsinfo1" v-show="errors.has('name9')"></span>
				</el-form-item>
				<!---->
				<el-form-item label="" class="box">
					<el-input type="textarea"  v-model="trackform.feedback" v-validate="'required'" name="name10"></el-input>
					<span class="tipsinfo1" v-show="errors.has('name10')"></span>
				</el-form-item>
				<!---->
				<el-form-item label="" class="box">
					<el-input type="textarea"  v-model="trackform.comment" v-validate="'required'" name="name11"></el-input>
					<span class="tipsinfo1" v-show="errors.has('name11')"></span>
				</el-form-item>
				
				<el-form-item label="" class="box">
				    <el-checkbox-group v-model="checkbox">
				        <el-checkbox :label="item.name" value="item.id" name="type" v-for="item in namelist"></el-checkbox>
					</el-checkbox-group>
				</el-form-item>
				
				<!--<el-form-item label="" id="addPerson">
				    <el-button type="primary" style="float: left;"></el-button>
				</el-form-item>-->
				
			</div>		
				
				<el-form-item>
				    <el-button type="primary" @click="onSubmit"></el-button>
				    <el-button></el-button>
				</el-form-item>

		</el-form>
		<!---->
		<!---->
			<el-upload class="upload-demo" ref="upload"
		  action="https://jsonplaceholder.typicode.com/posts/"
		  :on-preview="handlePreview"
		  :on-remove="handleRemove"
		  :file-list="fileList"
		  :auto-upload="false">
		  <el-button slot="trigger" size="small" type="primary"></el-button>
		  <el-button style="margin-left: 10px;" size="small" type="success" @click="submitUpload"></el-button>
		</el-upload>
		
	</div>
</template>

<script>
	export default {
	    data() {
	      return {
	      	id:'',
	      	checkbox:[],
	      	namelist:[],
	      	fileList: [],
	        trackform: {},
	        workCategoryList:[],
	        regionCategoryList:[],
	        feedbackCategoryList:[],
	        areaCategoryList:[]
//	        contactsList:[]//
	      }
	    },
	    methods: {
	      onSubmit() {
		       var that = this;
		       
		       var idList = [];//
		       for(var j = 0; j < this.checkbox.length; j++){//
		       		for(var i = 0; i < this.namelist.length; i++){
			       		if(this.checkbox[j] == this.namelist[i].name){//uuid
			       			idList.push(this.namelist[i].id)
			       		}
			        }
		       }
		       console.log(idList)
		     	that.trackform.investigators = idList;//
//		     	that.trackform.contactsList = that.contactsList
				that.trackform.id = that.uuid();//uuid
		       
		       that.trackform.surveyStartTime = Date.parse(new Date(that.trackform.surveyStartTime)).toString()//
		       
		       
		       
		       this.$validator.validateAll().then(function(res){//
		    		if(res){
		    			console.log(res)
		    			that.$http.SaveMarketUrl(that.trackform).then(function(data){
				        	console.log(data)
				       		 //
				    		that.$router.push({path:'marketTracking'});
				        }) .catch(function (error) {
						    console.log(error);
						});
		    		}
		    	})
		       
	      },
	      submitUpload() {
	        	this.$refs.upload.submit();
	      },
	      handleRemove(file, fileList) {
	        	console.log(file, fileList);
	      },
	      handlePreview(file) {
	        	console.log(file);
	      }
	    },
	    created(){
	    	var that = this;
	    	this.$http.MarketSearchCategory().then(function(data){//
//	    		console.log(data)
	    		that.areaCategoryList = data.data.areaCategoryList
	    		that.feedbackCategoryList = data.data.feedbackCategoryList
	    		that.regionCategoryList = data.data.regionCategoryList
	    		that.workCategoryList = data.data.workCategoryList
	    	})
	    	//
	    	this.$http.Investigator().then(function(data){
	    		console.log(data)
	    		that.namelist = data.data;
	    	})
	    }
 	 }
</script>

<style>
	#trackmarketinfo h5{
		color:#01a8a1;
		height: 20px;
	  border-bottom: 1px solid #01a8a1;
	  text-align: left;
	  line-height: 20px;
	}
	/**/
	#trackformTab{
		/*width: 50%;*/
		margin: auto;
	}
	/**/
	.el-form-item__content .el-select--mini{
		float: left;
	}
	/*label*/
	.el-form-item__label{
		margin-top:4px;
	}
	/**/
	.el-form .el-form-item{
		margin: 0;
	}
	/**/
	.el-table__header,.el-table__body{
		margin: auto;
	}
	/**/
	/*area*/
	.el-form .box .el-form-item--mini{
		width: 50%;
		margin: auto;
		margin:30px auto;
		
	}
	/*,*/
	#trackmarketinfo .el-form .box .el-checkbox-group label{
		float: left;
		width: 70px;
		margin-left: 0;
		text-align: left;		
	}
	#trackmarketinfo .el-form-item .el-form-item__label{
		width:100px!important;
	}
	#trackmarketinfo .el-form-item__content{
		margin-left: 100px!important;
	}
	/*textarea*/
	#trackmarketinfo .box textarea{
		height: 150px;
		margin-bottom: 18px;
	}
	#trackmarketinfo .text-box{
		max-width: 620px!important;
		margin: 10px auto;
	}
	
	/*margin*/
	#trackmarketinfo .upload-demo{
		margin-top: 30px;
	}
	
	/**/
	#trackmarketinfo .tipstd{
		position: relative
	}
	#trackmarketinfo .tipsinfo{
		font-size: 10px;
		color: red;
		position: absolute;
		top: 30px;
		left: 3px;
	}
	#trackmarketinfo .tipsinfo1{
		font-size: 10px;
		color: red;
		position: absolute;
		top: 145px;
		left: 3px;
	}
</style>


