<template>
	<div id="contractlist">
		<h5 ></h5>
		<el-form ref="form" :model="contractlistForm" label-width="80px" size="mini">
			<table id="contractlistFormtopTab">
				<!---->
				<tr>
					<td>
						<el-form-item label="" >
				  			<el-input v-model="contractlistForm.contractCode"></el-input>
				  		</el-form-item>
					</td>
					<td>
						<el-form-item label="">
				  			<el-input v-model="contractlistForm.contractCompany"></el-input>
				  		</el-form-item>
					</td>
				</tr>
				<!---->
				<tr>
					<td>
						<el-form-item label="">
					    	<el-input v-model="contractlistForm.projectName"></el-input>
					  	</el-form-item>
					</td>
				</tr>
				<!---->
				<tr>
					<td>
						<el-form-item label="">
			    			<el-select v-model="contractlistForm.area" placeholder="">
			      				<el-option label="" value=""></el-option>
			      				<el-option :label="item.category" :value="item.id" v-for="item in areaCategoryList"></el-option>
			    			</el-select>
			  			</el-form-item>
					</td>
					<td>
						<el-form-item label="">
			    			<el-select v-model="contractlistForm.region" placeholder="">
			      				<el-option label="" value=""></el-option>
			      				<el-option :label="item.category" :value="item.id" v-for="item in regionCategoryList "></el-option>
			   				</el-select>
			  			</el-form-item>
					</td>
				</tr>
				<!---->
				<tr>
					<td>
						<el-form-item label="" id="contractDate" >
					    	<el-col :span="11">
					    		<el-date-picker type="date" placeholder="" v-model="contractlistForm.startTime" style="width: 100%;"></el-date-picker>
					   	 	</el-col>
					    	<el-col class="line" :span="1">-</el-col>
					    	<el-col :span="11">
					    		<el-date-picker type="date" placeholder="" v-model="contractlistForm.endTime" style="width: 100%;"></el-date-picker>
					    	</el-col>
						</el-form-item>
					</td>
				</tr>
				<!---->
				<tr>
					<td>
						<el-form-item size="large">
						    <el-button type="primary" @click="onSubmit"></el-button>
						</el-form-item>
					</td>
				</tr>
			</table>
			<!---->
		</el-form>
	<!---->
	
		<el-table :data="contractlistFormtableData" id="contractlistFormtableData" border >
			<el-table-column  prop="contractCode" label="" width="150" height="30"></el-table-column>
			<el-table-column  prop="projectName" label="" width="150"></el-table-column>
			<el-table-column  prop="contractCompany" label="" width="150"></el-table-column>
			<el-table-column  prop="signDate" label="" sortable width="120"></el-table-column>
			<el-table-column  prop="preSignDate" label="" sortable width="150"></el-table-column>
			<el-table-column  prop="depositBackTime" label="" sortable width="150"></el-table-column>
			<el-table-column  prop="totalPrice" label="" width="100"></el-table-column>
			
			<el-table-column label="" width="100">
				<template slot-scope="scope">
					{{(scope.row.completeAmount/scope.row.totalQuantity).toFixed(2)*100+'%'}}
				</template>
			</el-table-column>
			
			<el-table-column label="" width="100">
				<template slot-scope="scope">
					{{(scope.row.paid/scope.row.totalPrice).toFixed(2)*100+'%'}}
				</template>
			</el-table-column>			
			
			<el-table-column  prop="areaCategoryModel.category" label="" width="80"></el-table-column>
			<el-table-column  prop="regionCategoryModel.category" label="" width="80"></el-table-column>
		    <el-table-column  label="" width="100">
		      	<template slot-scope="scope">
		        	<el-button @click="handleClick(scope.row)" type="text" size="small"></el-button>
		        	<el-button type="text" size="small" @click="edit(scope.row)"></el-button>
		      	</template>
		    </el-table-column>
		</el-table>
		
		<div class="block">
		    <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page.sync="currentPage1"
		      :page-size="10"
		      layout="total, prev, pager, next"
		      :total="totalNum">
		    </el-pagination>
		</div>
		
		<contractsee v-if="contractsee" v-on:contractsee2="contractsee2" :child-msg="id"></contractsee>
		<contractedit v-if="contractedit" v-on:contractedit2="contractedit2" :child-msg="id"></contractedit>
		
	</div>
</template>

<script>
	import contractsee from '@/components/contractsee'//
	import contractedit from '@/components/contractedit'//
	export default {
	
	components:{
		contractsee:contractsee,
		contractedit:contractedit
	},
    data() {
      return {
      	contractedit:false,
      	contractsee:false,
      	id:null,
      	totalNum:0,
      	currentPage1: 1,
        currentPage2: 5,
        currentPage3: 5,
        currentPage4: 4,
        contractlistForm: {
        	contractCode: '',
          	contractCompany: '',
          	projectName: '',
          	area:'',
        	region:'',
        	productCategory:'',
        	startTime:'',
        	endTime:'',
        	page:1,
        	rows:10,
        	sort:'signDate',
        	order:'desc'
        },
        postcontractlistform:{//
        	contractCode:'',
        	contractCompany:'',
        	projectName:'',
        	area:'',
        	region:'',
        	productCategory:'',
        	startTime:'',
        	endTime:'',
        	page:1,
        	rows:10,
        	sort:'signDate',
        	order:'desc'
        },
        contractlistFormtableData: [],
        areaCategoryList:[],
        regionCategoryList:[]
      };
    },
    methods: {
    	formatDate(date){//
      	if(!date){
      		return 
      	}
      		var date = new Date(date);
	      	var year = date.getFullYear();
	      	var month = date.getMonth();
	      	var day = date.getDate();
	      	return year + '-' + ((month+1)<10?'0'+(month+1):(month+1)) + '-' +(day<10?'0'+day:day)
     	},
    	onSubmit() {
        	this.currentPage1 = 1;
      		this.postcontractlistform = JSON.parse(JSON.stringify(this.contractlistForm));//
	        var fd = new FormData();
	    	fd.append("contractCode",this.postcontractlistform.contractCode)
	    	fd.append("contractCompany",this.postcontractlistform.contractCompany)
	    	fd.append("projectName",this.postcontractlistform.projectName)
	    	fd.append("area",this.postcontractlistform.area)
	    	fd.append("region",this.postcontractlistform.region)
	    	fd.append("productCategory",this.postcontractlistform.productCategory)
	    	fd.append("startTime",this.formatDate(this.postcontractlistform.startTime))//
	    	fd.append("endTime",this.formatDate(this.postcontractlistform.endTime))
	    	fd.append("page",this.postcontractlistform.page)
	    	fd.append("rows",this.postcontractlistform.rows)
	    	fd.append("sort",this.postcontractlistform.sort)
	    	fd.append("order",this.postcontractlistform.order)
	    	
	    	var that = this
	    	this.$http.Contractlist(fd).then(function(data){
	    		that.totalNum = data.data.total;//
		  		var list = data.data.rows;//
		  		for(var i = 0; i < list.length; i++){//XX-XX-XX
					list[i].signDate = that.NumConvertUtil.formatDate2(list[i].signDate)//
					list[i].preSignDate = that.NumConvertUtil.formatDate2(list[i].preSignDate)//
					list[i].depositBackTime = that.NumConvertUtil.formatDate2(list[i].depositBackTime)//
				}
				that.contractlistFormtableData = list;//
	    		
	    	})
	    	
	    	
      	},
     	handleSizeChange(val) {
        	console.log(` ${val} `);
      	},
      	handleCurrentChange(val) {//
        	console.log(`: ${val}`);
        	//formData
	    	var fd = new FormData();
	    	fd.append("contractCode",this.postcontractlistform.contractCode)
	    	fd.append("contractCompany",this.postcontractlistform.contractCompany)
	    	fd.append("projectName",this.postcontractlistform.projectName)
	    	fd.append("area",this.postcontractlistform.area)
	    	fd.append("region",this.postcontractlistform.region)
	    	fd.append("productCategory",this.postcontractlistform.productCategory)
	    	fd.append("startTime",this.postcontractlistform.startTime)
	    	fd.append("endTime",this.postcontractlistform.endTime)
	    	fd.append("page",val)
	    	fd.append("rows",this.postcontractlistform.rows)
	    	fd.append("sort",this.postcontractlistform.sort)
	    	fd.append("order",this.postcontractlistform.order)
	    	
	    	var that = this
	    	this.$http.Contractlist(fd).then(function(data){
	    		that.totalNum = data.data.total;//
		  		var list = data.data.rows;//
		  		for(var i = 0; i < list.length; i++){//XX-XX-XX
					list[i].signDate = that.NumConvertUtil.formatDate2(list[i].signDate)//
					list[i].preSignDate = that.NumConvertUtil.formatDate2(list[i].preSignDate)//
					list[i].depositBackTime = that.NumConvertUtil.formatDate2(list[i].depositBackTime)//
				}
				that.contractlistFormtableData = list;//
	    		
	    	})
        	
		},
		handleClick(scope){
			console.log(scope)//id
			this.id = scope.id
			this.contractsee=true
		},
		edit(scope){//
			this.contractedit=true
			this.id = scope.id
		},
		contractsee2(res){
    		this.contractsee = res//
    	},
    	contractedit2(res){
    		this.contractedit = res//
    	}
    },
    created(){
    	//
    	this.$http.ContractTypeinfo().then(function(data){
    		console.log(data)
    		that.areaCategoryList = data.data.areaCategoryList//
    		that.regionCategoryList = data.data.regionCategoryList//
    	})
    	
    	
    	
    	
    	//formData
    	var fd = new FormData();
    	fd.append("contractCode",this.postcontractlistform.contractCode)
    	fd.append("contractCompany",this.postcontractlistform.contractCompany)
    	fd.append("projectName",this.postcontractlistform.projectName)
    	fd.append("area",this.postcontractlistform.area)
    	fd.append("region",this.postcontractlistform.region)
    	fd.append("productCategory",this.postcontractlistform.productCategory)
    	fd.append("startTime",this.postcontractlistform.startTime)
    	fd.append("endTime",this.postcontractlistform.endTime)
    	fd.append("page",this.postcontractlistform.page)
    	fd.append("rows",this.postcontractlistform.rows)
    	fd.append("sort",this.postcontractlistform.sort)
    	fd.append("order",this.postcontractlistform.order)
    	
    	var that = this
    	this.$http.Contractlist(fd).then(function(data){
    		console.log(data)
    		that.totalNum = data.data.total;//
	  		var list = data.data.rows;//
	  		for(var i = 0; i < list.length; i++){//XX-XX-XX
				list[i].signDate = that.NumConvertUtil.formatDate2(list[i].signDate)//
				list[i].preSignDate = that.NumConvertUtil.formatDate2(list[i].preSignDate)//
				list[i].depositBackTime = that.NumConvertUtil.formatDate2(list[i].depositBackTime)//
			}
			that.contractlistFormtableData = list;//
    		
    	})
    }
  };
	
</script>

<style>
	#contractlist h5{
		color:#01a8a1;
		height: 20px;
	    border-bottom: 1px solid #01a8a1;
	    text-align: left;
	    line-height: 20px;
	}
	
	
	#contractlistFormtopTab{
		margin: auto;
	}
	/**/
	.el-form-item__content .el-select--mini{
		float: left;
	}
	/**/
	.el-button--large{
		float: left;
		margin:10px 0;
	}
	/*table*/
	.el-table__header-wrapper{
		line-height: 0px;
	}
	/**/
	.cell{
		text-align: center;
	}
	/*label*/
	.el-form-item__label{
		margin-top:4px;
	}
	.el-form-item__label:nth-of-type(6){
		margin-top:none;
		color: red;
	}
	/**/
	.el-form .el-form-item{
		margin: 0;
	}
	/**/
	.el-table{
		background: none;
	}
	/**/
	.el-table__header,.el-table__body{
		margin: auto;
	}
	/**/
	
	.el-form-item--mini{
		/*width: 80%;*/
		margin: auto;
	}
	/*label*/
	#contractlistFormtopTab .el-form-item .el-form-item__label{
		width:130px!important;
	}
	#contractlistFormtopTab .el-form-item__content{
		margin-left: 130px!important;
	}
	#contractlistFormtableData{
		max-width: 1501px;
		margin: 0 auto;
	}
</style>


