import Vue from 'vue'
import Router from 'vue-router'//
import HelloWorld from '@/components/HelloWorld'//
import home from '@/components/Home'//

import marketInformation from '@/components/marketInformation'//
import marketTracking from '@/components/marketTracking'//
import relationship from '@/components/relationship'//
import trackinginformation from '@/components/TrackingInformation'//
import addpersoninfomation from '@/components/addPersonInfomation'//


import contractinfo from '@/components/contractinfo'//
import contractlist from '@/components/contractlist'//
import addcontract from '@/components/addcontract'//


import BiddingInfo from '@/components/BiddingInfo'//
import Biddinglist from '@/components/Biddinglist'//
import addBidding from '@/components/addBidding'//


import usermanagement from '@/components/usermanagement'//
import adduser from '@/components/adduser'//


Vue.use(Router)//

export default new Router({
  routes: [
    {
      path: '/',//
      name: 'HelloWorld',
      component: HelloWorld
    },
    //
    {
      path: '/home',//
      name: 'home',
      component: home,
      children:[
      	{
      		path:'marketInformation',//
      		name:'marketInformation',
      		component:marketInformation
      	},
      	{
      		path:'marketTracking',//
      		name:'marketTracking',
      		component:marketTracking
      	},
      	{
      		path:'relationship',//
      		name:'relationship',
      		component:relationship
      	},
      	{
      		path:'trackinginformation',//
      		name:'trackinginformation',
      		component:trackinginformation
      	},
      	{
      		path:'addpersoninfomation',//
      		name:'addpersoninfomation',
      		component:addpersoninfomation
      	},
      	{
      		path:'contractinfo',//
      		name:'contractinfo',
      		component:contractinfo
      	},
      	{
      		path:'contractlist',//
      		name:'contractlist',
      		component:contractlist
      	},
      	{
      		path:'addcontract',//
      		name:'addcontract',
      		component:addcontract
      	},
      	{
      		path:'BiddingInfo',//
      		name:'BiddingInfo',
      		component:BiddingInfo
      	},
      	{
      		path:'Biddinglist',//
      		name:'Biddinglist',
      		component:Biddinglist
      	},
      	{
      		path:'addBidding',//
      		name:'addBidding',
      		component:addBidding
      	},
      	{
      		path:'usermanagement',//
      		name:'usermanagement',
      		component:usermanagement
      	},
      	{
      		path:'adduser',//
      		name:'adduser',
      		component:adduser
      	}
      ]
    }
  ]
})



