<template>
	<div id="relationship">
		<h5 class="personlist"></h5>
		<div class="diyihang">
			<el-form :inline="true" :model="personform" class="demo-form-inline" size="mini">
						<el-form-item>
		    				<el-input  placeholder="" v-model="personform.name"></el-input>
		   				 </el-form-item>
						<el-form-item>
		    				<el-input  placeholder=""  v-model="personform.company"></el-input>
		    			</el-form-item>
						<el-form-item>
		    				<el-button type="primary" @click="onSubmit"></el-button>
						</el-form-item>
			</el-form>
		</div>
		<!---->
		<el-table :data="PersontableData" border style="max-width: 1200px;margin:0 auto;margin-top:30px;">
			<el-table-column  prop="name" label="" width="100" height="30"></el-table-column>
			<el-table-column  prop="company" label="" width="180"></el-table-column>
			<el-table-column  prop="user.name" label="" width="100"></el-table-column>
			<el-table-column  prop="job" label="" width="180"></el-table-column>
			<el-table-column  prop="mobile" label="" width="130"></el-table-column>
			<el-table-column  prop="inputTime" label="" sortable width="130"></el-table-column>
			<el-table-column  prop="importanceLevel" label="" width="100"></el-table-column>
		    <el-table-column  label="" width="100">
		      	<template slot-scope="scope">
		        	<el-button @click="handleClick(scope.row)" type="text" size="small"></el-button>
		        	<el-button @click="personeditclick(scope.row)" type="text" size="small"></el-button>
		      	</template>
		    </el-table-column>
		</el-table>
		<!---->
		<!---->
		<div class="block">
		   <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page.sync="currentPage1"
	      :page-size="10" layout="total, prev, pager, next" :total="totalNum">
	    </el-pagination>
		</div>
		
		
		
		<personsee v-if="personsee2" :child-msg="id" v-on:personsee="personsee"></personsee>
		<personedit v-if="personedit2" :child-msg="id" v-on:personedit="personedit"></personedit>
	</div>
</template>

<script>
	import personsee from '@/components/personsee'//
	import personedit from '@/components/personedit'//
	export default {
		components:{
			personsee:personsee,//
			personedit:personedit//
		},
	    data() {
	    	return {
	    		personedit2:false,
	    		personsee2:false,
	    		id:null,
	    		totalNum:0,
	    		personform:{
	    			name:'',
	    			company:''
	    		},
	        	postform: {
	          		name:'',//null 
	    			company:'',
	    			sort:"inputTime",
        			order:"desc",
        			page:1,
        			rows:10,
	        	},
	        	PersontableData: [],
		        currentPage1: 1,
		        currentPage2: 5,
		        currentPage3: 5,
		        currentPage4: 4
	      	}
	    },
	    methods: {
	      	onSubmit() {
	        	console.log('submit!');
	        	this.currentPage1 = 1;
	        	this.postform.name = JSON.parse(JSON.stringify(this.personform.name));
	        	this.postform.company = JSON.parse(JSON.stringify(this.personform.company));
	       		var fd = new FormData();//formData
		    	fd.append("name",this.postform.name)
		    	fd.append("company",this.postform.company)
		    	fd.append("sort",this.postform.sort)
		    	fd.append("order",this.postform.order)
		    	fd.append("page",1)
		    	fd.append("rows",this.postform.rows)
		    		
		    	var that = this;//this

			    this.$http.ContactListUrl(fd).then(function(data){
			    	console.log(data)
			    	that.totalNum = data.data.total;//
			    	var list = data.data.rows;
			    	for(var i = 0; i < list.length; i++){//XX-XX-XX
					list[i].inputTime = that.NumConvertUtil.formatDate2(list[i].inputTime)
				}
			    	that.PersontableData = list;
			    })
	      	},
	      	handleSizeChange(val) {
		        console.log(` ${val} `);
		    },
		    handleCurrentChange(val) {
		        console.log(`: ${val}`);
		        var fd = new FormData();//formData
			    var that = this;//this
		    	fd.append("name",this.postform.name)
		    	fd.append("company",this.postform.company)
		    	fd.append("sort",this.postform.sort)
		    	fd.append("order",this.postform.order)
		    	fd.append("page",val)
		    	fd.append("rows",this.postform.rows)
		    		
			    this.$http.ContactListUrl(fd).then(function(data){
			    	console.log(data)
			    	var list = data.data.rows;
			    	for(var i = 0; i < list.length; i++){//XX-XX-XX
						list[i].inputTime = that.NumConvertUtil.formatDate2(list[i].inputTime)
				    }
			    	that.PersontableData = list;
			    })
		    },
		    handleClick(scope){//
		      	this.personsee2 =true
		      	console.log(scope)
		      	this.id = scope.id;//id
		    },
		    personeditclick(scope){//
		      	this.personedit2 =true
		      	this.id = scope.id;//id
		    },
		    personsee(res){
    			this.personsee2 = res//
    		},
    		personedit(res){
    			this.personedit2 =res//
    		}
	    },
	    created(){
	    	var fd = new FormData();//formData
	    	var that = this;//this
    		fd.append("name",this.postform.name)
    		fd.append("company",this.postform.company)
    		fd.append("sort",this.postform.sort)
    		fd.append("order",this.postform.order)
    		fd.append("page",this.postform.page)
    		fd.append("rows",this.postform.rows)
    		
	    	this.$http.ContactListUrl(fd).then(function(data){
	    		console.log(data)
	    		var list = data.data.rows;
	    		that.totalNum = data.data.total;//
	    		for(var i = 0; i < list.length; i++){//XX-XX-XX
				list[i].inputTime = that.NumConvertUtil.formatDate2(list[i].inputTime)
			}
	    		that.PersontableData = list;
	    	})
	    }
  	}
	
</script>

<style>
	
	#relationship .personlist{
		color:#01a8a1;
		height: 20px;
	    border-bottom: 1px solid #01a8a1;
	    text-align: left;
	    line-height: 20px;
	}
	
	
	.diyihang{
		width:100%;
		height: 28px;
		
	}
	.el-table__header-wrapper{
		line-height: 0px;	
	}
	/**/
	.el-table{
		background: none;
	}
	/**/
	.el-table__header,.el-table__body{
		margin: auto;
	}
	.cell{
		text-align: center;
	}
</style>


