<template>
	<el-col :span="12">
	    <el-menu
	      default-active="2"
	      class="el-menu-vertical-demo"
	      @open="handleOpen"
	      @close="handleClose"
	      background-color="#545c64"
	      text-color="#fff"
	      active-text-color="#ffd04b">
	      
	      
	      <el-submenu index="1">
	        <template slot="title">
	          <i class="el-icon-location"></i>
	          <span class="yijibiaoti"></span>
	        </template>
	        <el-menu-item-group>
	          <el-menu-item index="1-1" @click="toMarketInfo"></el-menu-item>
	          <el-menu-item index="1-2" @click="toMarketTarck"></el-menu-item>
	          <el-menu-item index="1-3" @click="toRelationship"></el-menu-item>
	          <el-menu-item index="1-4" @click="toTrackInfo"></el-menu-item>
	          <el-menu-item index="1-5" @click="toAddPersonInfo"></el-menu-item>
	        </el-menu-item-group>
	      </el-submenu>
	      
	      <el-submenu index="2">
	        <template slot="title">
	          <i class="el-icon-menu"></i>
	          <span class="yijibiaoti"></span>
	        </template>
	        <el-menu-item-group>
	          <el-menu-item index="2-1" @click="toContractInfo"></el-menu-item>
	          <el-menu-item index="2-2" @click="toContractlist"></el-menu-item>
	          <el-menu-item index="2-3" @click="toAddContractInfo"></el-menu-item>
	        </el-menu-item-group>
	      </el-submenu>
	      
	      
	       <el-submenu index="3">
	        <template slot="title">
	          <i class="el-icon-document"></i>
	          <span class="yijibiaoti"></span>
	        </template>
	        <el-menu-item-group>
	          <el-menu-item index="3-1" @click="toBiddingInfo"></el-menu-item>
	          <el-menu-item index="3-2" @click="toBiddinglist"></el-menu-item>
	          <el-menu-item index="3-3" @click="addBiddingInfo"></el-menu-item>
	        </el-menu-item-group>
	      </el-submenu>
	      
	      
	       <el-submenu index="4">
	        <template slot="title">
	          <i class="el-icon-setting"></i>
	          <span class="yijibiaoti"></span>
	        </template>
	        <el-menu-item-group>
	          <el-menu-item index="4-1" @click="userManagement"></el-menu-item>
	          <el-menu-item index="4-2" @click="addUser"></el-menu-item>
	        </el-menu-item-group>
	      </el-submenu>
	      
	    </el-menu>
	  </el-col>
	</el-row>
</template>

<script>
	export default{
		name:'Leftaside',
		methods: {
	      handleOpen(key, keyPath) {
	        console.log(key, keyPath);
	      },
	      handleClose(key, keyPath) {
	        console.log(key, keyPath);
	      },
	      toMarketInfo:function(){//
	      	this.$router.push({
	      		path:'/home/marketInformation',
	      		query:{}
	      	})
	      },
	      toMarketTarck:function(){//
	      	this.$router.push({
	      		path:'/home/marketTracking',
	      		query:{}
	      	})
	      },
	      toRelationship:function(){//
	      	this.$router.push({
	      		path:'/home/relationship',
	      		query:{}
	      	})
	      },
	      toTrackInfo:function(){//
	      	this.$router.push({
	      		path:'/home/trackinginformation',
	      		query:{}
	      	})
	      },
	      toAddPersonInfo:function(){//
	      	this.$router.push({
	      		path:'/home/addPersonInfomation',
	      		query:{}
	      	})
	      },
	      toContractInfo:function(){//
	      	this.$router.push({
	      		path:'/home/contractinfo',
	      		query:{}
	      	})
	      },
	      toContractlist:function(){//
	      	this.$router.push({
	      		path:'/home/contractlist',
	      		query:{}
	      	})
	      },
	      toAddContractInfo:function(){//
	      	this.$router.push({
	      		path:'/home/addcontract',
	      		query:{}
	      	})
	      },
	       toBiddingInfo:function(){//
	      	this.$router.push({
	      		path:'/home/BiddingInfo',
	      		query:{}
	      	})
	      },
	       toBiddinglist:function(){//
	      	this.$router.push({
	      		path:'/home/Biddinglist',
	      		query:{}
	      	})
	      },
	       addBiddingInfo:function(){//
	      	this.$router.push({
	      		path:'/home/addBidding',
	      		query:{}
	      	})
	      },
	       userManagement:function(){//
	      	this.$router.push({
	      		path:'/home/usermanagement',
	      		query:{}
	      	})
	      },
	       addUser:function(){//
	      	this.$router.push({
	      		path:'/home/adduser',
	      		query:{}
	      	})
	      }
	    }
	}
</script>

<style>
	.el-menu-vertical-demo{
		width: 200px;
	}
	.yijibiaoti{
		padding-right: 70px;
	}
	
	.el-submenu__title i{
		background:#122634!important;
	}
	
	.el-submenu__title{
		background:#122634!important;	
		color: #8297a5!important;
	}
	.el-submenu__title:hover{
		background: #1d3c4d!important;
	}
	.el-menu-item{
		background:#122634!important;
		color: #8297a5!important;	
	}
	.el-menu-item:hover{
		background:#1d3c4d!important;	
		color: #FFFFFF!important;
	}
	.el-menu-item.is-active{
		background:#1d3c4d!important;	
		color: #FFFFFF!important;	
	}
	.el-menu-item-group__title{
		background:#122634!important;	
	}
	.el-submenu__title i{
	 	color: #8297a5;	
	}
</style>


