<template>
	<div id="addBiddingMarket">
		<h5></h5>
		<el-form ref="form" :model="addBiddingform" label-width="80px" size="mini">
			<table id="addBiddingMarketTab">
				<!---->
				<tr>
					<td>
						<el-form-item label="" >
				  			<el-input v-model="addBiddingform.bidCompany" v-validate="'required'" name="name1"></el-input>
				  			<span class="tipsinfo" v-show="errors.has('name1')"></span>
				  		</el-form-item>
					</td>
					<td>
						<el-form-item label="">
				  			<el-input  v-model="addBiddingform.bidProject"  v-validate="'required'" name="name2"></el-input>
				  			<span class="tipsinfo" v-show="errors.has('name2')"></span>
				  		</el-form-item>
					</td>
				</tr>
				<!---->
				<tr>
					<td>
						<el-form-item label="" >
				  			<el-input v-model="addBiddingform.bidAmount"></el-input>
				  		</el-form-item>
					</td>
				</tr>
				<!---->
				<tr>
					<td>
						<el-form-item label="">
				  			<el-date-picker type="date" placeholder="" v-model="addBiddingform.bidTime" v-validate="'required'" name="date"></el-date-picker>
				  			<span class="tipsinfo" v-show="errors.has('date')"></span>
				  		</el-form-item>
					</td>
					<td>
						<el-form-item label="" >
				  			<el-select v-model="addBiddingform.status" placeholder="">
			      				<el-option label="" value=""></el-option>
			      				<el-option :label="item.category" :value="item.id" v-for="item in bidAuditCategoryList"></el-option>
			    			</el-select>
				  		</el-form-item>
					</td>
				</tr>
				
				<!---->
				<tr>
					<td>
						<el-form-item label="">
			    			<el-select v-model="addBiddingform.area" placeholder="" >
			      				<el-option label="" value=""></el-option>
			      				<el-option :label="item.category" :value="item.id" v-for="item in areaCategoryList"></el-option>
			    			</el-select>
			  			</el-form-item>
					</td>
					<td>
						<el-form-item label="">
			    			<el-select v-model="addBiddingform.region" placeholder="">
			      				<el-option label="" value=""></el-option>
			      				<el-option :label="item.category" :value="item.id" v-for="item in regionCategoryList"></el-option>
			   				</el-select>
			  			</el-form-item>
					</td>
				</tr>
				<!---->
				
			</table>
			
			<div class="text-box-addBidding">
				<!---->
				<el-form-item label="" class="box">
				    <el-input type="textarea"  v-model="addBiddingform.comment"></el-input>
				</el-form-item>
				<!---->
				<el-form-item label="" class="box">
					<el-input type="textarea"  v-model="addBiddingform.bidSummary"></el-input>
				</el-form-item>
				
				<el-form-item label="" id="addPerson">
				    <el-button type="primary" style="float: left;" @click="addlist"></el-button>
				</el-form-item>
				<!---->
				<!---->
				<table  id="insidedata">
        					<tr >
        						<th class="firsttr" align="center" width="16.66%" height=20></th>
		                        <th class="firsttr" align="center" width="16.66%" height=20></th>
		                        <th class="firsttr" align="center" width="16.66%" height=20></th>
		                        <th class="firsttr" align="center" width="16.66%" height=20></th>
		                        <th class="firsttr" align="center" width="16.66%" height=20></th>
		                        <th class="firsttr" align="center" width="16.66%" height=20></th>
		                        <th class="firsttr" align="center" width="16.66%" height=20></th>
        					</tr>
        					<tr v-for="(item,index) in arrList">
        						<td class="secondtr" align="center" width="16.66%" height=20 >
        							<input type="text" class="inpu" v-model="item.pkgNum"/>
        						</td>
        						<td class="secondtr" align="center" width="16.66%" height=20>
        							<select class="inpu" v-model="item.productCategory">
        								<option :value="item.id" :label="item.category" v-for="item in productCategoryList"></option>
        							</select>
        						</td>
        						
        						<td class="secondtr" align="center" width="16.66%" height=20>
        							<input type="text"  class="inpu" v-model="item.amount"/>
        						</td>
        						<td class="secondtr" align="center" width="16.66%" height=20>
        							<select class="inpu" v-model="item.unit">
        								<option :value="item.id" :label="item.category" v-for="item in priceUnitCategoryList"></option>
        							</select>
        						</td>
        						<td class="secondtr" align="center" width="16.66%" height=20>
        							<input type="text"  class="inpu" v-model="item.bidder"/>
        						</td>
        						<td class="secondtr" align="center" width="16.66%" height=20>
        							<input type="text"  class="inpu" v-model="item.tenderPrice"/>
        						</td>
        						<td class="secondtr" align="center" width="16.66%" height=20>
        							<input type="button" value="" class="del" @click="delatetr(index)"/>
        						</td>
        					</tr>
        				</table>
				
			</div>		
				
				<el-form-item>
				    <el-button type="primary" @click="onSubmit"></el-button>
				    <el-button></el-button>
				</el-form-item>

		</el-form>
		<!---->
			<el-upload class="upload-demo" ref="upload" action="https://jsonplaceholder.typicode.com/posts/" :on-preview="handlePreview"
		    :on-remove="handleRemove"
		    :file-list="fileList"
		    :auto-upload="false">
		    <el-button slot="trigger" size="small" type="primary"></el-button>
		    <el-button style="margin-left: 10px;" size="small" type="success" @click="submitUpload"></el-button>
		</el-upload>
		
	</div>
</template>

<script>
	export default {
	    data() {
	      	return {
	      		id:'',
		      	listshow:0,
		      	fileList: [],
		        addBiddingform: {},
	        	areaCategoryList:[],
	       	 	regionCategoryList:[],
	        	bidAuditCategoryList:[],
	        	productCategoryList:[],
	        	priceUnitCategoryList:[],
	        	bidPkgList:[],
	        	arrList:[{//
	        		pkgNum:null,
	        		amount:null,
	        		bidder:'',
	        		productCategory:null,
	        		tenderPrice:null,
	        		unit:null
	        		
	        	}]
	      }
	    },
	    methods: {
	    	addlist(){
	    		this.arrList.push({//
	    			pkgNum:null,
	        		amount:null,
	        		bidder:'',
	        		productCategory:null,
	        		tenderPrice:null,
	        		unit:null
	    		})
	    	},
	    	delatetr(index){
	    		if(index==0){//
	    			this.arrList[index].pkgNum = null;
	    			this.arrList[index].amount = null;
	    			this.arrList[index].bidder = '';
	    			this.arrList[index].productCategory = null;
	    			this.arrList[index].tenderPrice = null;
	    			this.arrList[index].unit = null;
	    			return
	    		}
	    		this.arrList.splice(index,1)//1
	    	},
	      	onSubmit() {
	      		var that = this;
	      		
	      		//
	      		for(var i = 0; i < this.arrList.length; i++){
	      			for(var key in this.arrList[i]){
	      				if(this.arrList[i][key]){
	      					this.bidPkgList.push(this.arrList[i])
	      					break;
	      				}
	      			}
	      		}
	      		
	      	
	      		that.addBiddingform.bidPkgList = that.bidPkgList
	      		
	        	that.addBiddingform.id = this.uuid();//uuid
	        	
	        	

	        	
	        	
	        	
	        	this.$validator.validateAll().then(function(res){//
		    		if(res){
		    			
		    			that.addBiddingform.bidTime = Date.parse(new Date(that.addBiddingform.bidTime))//
		    			
		    			console.log(res)
		    			that.$http.bidAddUrl(that.addBiddingform).then(function(data){
				        	console.log(data)
				       		 //
				    		that.$router.push({path:'Biddinglist'});
				        }) .catch(function (error) {
						    console.log(error);
						});
		    		}
		    	})
	      	},
		    submitUpload() {
	        	this.$refs.upload.submit();
	      	},
	      	handleRemove(file, fileList) {
	        	console.log(file, fileList);
	      	},
	      	handlePreview(file) {
	        	console.log(file);
	     	}
	    },
	    created(){
	    		var that = this;
	    		this.$http.allCategory().then(function(data){//
//	    		console.log(data)
	    		that.areaCategoryList = data.data.areaCategoryList
	    		that.regionCategoryList = data.data.regionCategoryList
	    		that.bidAuditCategoryList = data.data.bidAuditCategoryList
	    		that.productCategoryList = data.data.productCategoryList
	    		that.priceUnitCategoryList = data.data.priceUnitCategoryList
	    	})
	    }
 	 }
</script>

<style>
	
	#addBiddingMarket h5{
		color:#01a8a1;
		height: 20px;
	    border-bottom: 1px solid #01a8a1;
	    text-align: left;
	    line-height: 20px;
	}
	/**/
	#addBiddingMarketTab{
		/*width: 50%;*/
		margin: auto;
	}
	/**/
	.el-form-item__content .el-select--mini{
		float: left;
	}
	/*label*/
	.el-form-item__label{
		margin-top:4px;
	}
	/**/
	.el-form .el-form-item{
		margin: 0;
	}
	/**/
	.el-table__header,.el-table__body{
		margin: auto;
	}
	/**/
	/*area*/
	#addBiddingMarket .el-form .box .el-form-item--mini{
		width: 50%;
		margin: auto;
		margin:30px auto;
		
	}
	/*,*/
	#addBiddingMarket .el-form .box .el-checkbox-group label{
		float: left;
		width: 70px;
		margin-left: 0;
		text-align: left;		
	}
	#addBiddingMarket .el-form-item .el-form-item__label{
		width:100px!important;
	}
	#addBiddingMarket .el-form-item__content{
		margin-left: 100px!important;
	}
	/*textarea*/
	#addBiddingMarket .box textarea{
		height: 150px;
		margin-bottom: 18px;
	}
	#addBiddingMarket .text-box-addBidding{
		max-width: 646px!important;
		margin: 0 auto;
	}
	#addBiddingMarket .el-select--mini{
		width:220px
	}
	#addBiddingMarket .upload-demo{
		margin: auto;
		margin-top: 80px;
	}
	
	
	#addBiddingMarket #insidedata{
		margin-top: 10px;
		margin-bottom: 10px;
		width: 100%;
		border-collapse:collapse;
		border: 1px solid #ebeef5;
		font-size: 14px;
   		color: #606266;
	}
	#addBiddingMarket #insidedata .firsttr{
		background: #fff;
	}
	#addBiddingMarket #insidedata td{
		border: 1px solid #ebeef5;
	}
	#addBiddingMarket #insidedata .inpu{
		width: 100px;
		border: 1px solid #D3DCE6;
	}
	#addBiddingMarket #insidedata .del{
		width: 50px;
		background: #01a8a1;
		border: none;
		color: #FFFFFF;
		font-size: 12px;
	}
	
	
	/**/
	#addBiddingMarket .tipstd{
		position: relative
	}
	#addBiddingMarket .tipsinfo{
		font-size: 10px;
		color: red;
		position: absolute;
		top: 30px;
		left: 3px;
	}
	#addBiddingMarket .el-button--small{
		color:white !important;
	}
	
</style>


