<template>
	<div>
		<div id="echartMarket">
		    <!--echarts-->
		    <div id="seaseonContainer" style="width:500px; height:500px"></div>
		   	<div id="monthContainer" style="width:500px; height:500px"></div>
		   	<div id="diquContainer" style="width:500px; height:500px"></div>
		   	<div id="pianquContainer" style="width:500px; height:500px"></div>
		</div>
	</div>
</template>

<script>
//	var echarts = require('echarts');//echarts
	export default{
		name:'marketInformation',
		data(){
			return{
			}
		},
		mounted() {
			//this.echarts echarts
			var seaseonContainer = this.echarts.init(document.getElementById('seaseonContainer'));//
			var monthContainer = this.echarts.init(document.getElementById('monthContainer'));//
			var diquContainer = this.echarts.init(document.getElementById('diquContainer'));//
			var pianquContainer = this.echarts.init(document.getElementById('pianquContainer'));//
			
			
			
			//
				 seaseonContainer.setOption({
				 	title: {
                    show:true,
                    x:'center',
                    text: ''
	                },
	                color: ['#003366', '#006699', '#4cabce', '#4cacaa'],//
	                tooltip: {},//
	                legend: {
	                    y:'bottom',
	                    data:['','','',''],
	
	                },
	                xAxis: {
	                    data: []
	                },
	                yAxis: {
	                    type : 'value',
	                    axisLabel:{formatter:'{value} '}
	
	                },
	
	                series: [
	                    {
	                        barGap:'5%',
	                        name: '',
	                        type: 'bar',
	                        data: [],
	                        itemStyle:{
	                            normal:{
	                                label:{
	                                    show:true,
	                                    position:'top',
	                                    textStyle:{
	                                        fontSize : '15',
	                                        fontFamily : '',
	                                    }
	                                }
	                            }
	                        },
	                    },
	                    {
	                        name: '',
	                        type: 'bar',
	                        data: [],
	                        itemStyle:{
	                            normal:{
	                                label:{
	                                    show:true,
	                                    position:'top',
	                                    textStyle:{
	                                        fontSize : '15',
	                                        fontFamily : '',
	                                    }
	                                }
	                            }
	                        },
	                    },
	                    {
	                        name: '',
	                        type: 'bar',
	                        data: [],
	                        itemStyle:{
	                            normal:{
	                                label:{
	                                    show:true,
	                                    position:'top',
	                                    textStyle:{
	                                        fontSize : '15',
	                                        fontFamily : '',
	                                    }
	                                }
	                            }
	                        },
	                    },
	                    {
	                        name: '',
	                        type: 'bar',
	                        data: [],
	                        itemStyle:{
	                            normal:{
	                                label:{
	                                    show:true,
	                                    position:'top',
	                                    textStyle:{
	                                        fontSize : '15',
	                                        fontFamily : '',
	                                    }
	                                }
	                            }
	                        },
	                    }
	
	                ]

			    });
			
			//
			this.$http.seasonVisit().then(function(res){
				console.log(res)
				 seaseonContainer.setOption({
			        xAxis: {
			            data:res.data.years
			        },
			        series: [
				            {
	                            name: '',
	                            data: res.data.q1
	                        },
	                        {
	                            name: '',
	                            data: res.data.q2
	                        },
	                        {
	                            name: '',
	                            data: res.data.q3
	                        },
	                        {
	                            name: '',
	                            data: res.data.q4
	                        }
			        ]
			    });
			})
				
			//*/*/*/*/*/*/*/**/*/*/*/*/*/*/*/




			//
			monthContainer.setOption({
			    title: {
			        text: '',
			        x:'center',
			    },
			    color: ['#4cacaa'],//
			    tooltip: {},
			    legend: {
			        data:[''],
			         y:'bottom',
			    },
			    xAxis: {
			        data: []
			    },
			    yAxis: {
	                    type : 'value',
	                    axisLabel:{formatter:'{value} '}
	
	            },
			    series: [{
			        name: '',
			        type: 'bar',
			        data: [],
			        itemStyle:{
                            normal:{
                                label:{
                                    show:true,
                                    position:'top',
                                    textStyle:{
                                        fontSize : '15',
                                        fontFamily : '',
                                    }
                                }
                            }
                        }
			    }]
			});
			
			// 
			this.$http.MonthVisit().then(function(res){//
				console.log(res)
				 monthContainer.setOption({
			        xAxis: {
			            data:res.data.titles
			        },
			        series: [{
			            // 
			            name:'',
			            data:res.data.pillars
			        }]
			    });
			})
			/*************/
			
			//
				diquContainer.setOption({
		            title: {
		                show:true,
		                x:'center',
		                text: ''
		            },
//		             color: ['#003366', '#006699', '#4cabce'],//
		            legend: {
		                y:'bottom',
		                data:[],
		            },
		            series: [
		                {
		                    name:'',
		                    type:'pie',
		                    radius: ['30%', '40%'],
		                    legendHoverLink: false, //
		                    hoverAnimation:false,//
		                    label: {
		                        show:true,
		                        position:'outside',
		
		                    },
		                    labelLine: {
		                        show:true,
		                        length:50,
		
		                    },
		                    data:[],
		                    itemStyle:{
		                        normal:{
		                            label:{
		                                show: true,
		                                formatter: '{b} : {c}({d}%)'
		                            },
		                            labelLine :{show:true}
		                        }
		                    }
		                }
		            ]
		
		        });
		        diquContainer.showLoading();
		        
		        //
			    this.$http.diquVisit().then(function(res){//
			    		console.log(res)
			            diquContainer.hideLoading();
			            diquContainer.setOption({
			                legend: {
			                    data: res.data.category
			                },
			                series: [
			                    {
			                        name: '',
			                        data: res.data.pie,
			                    }
			                ]
			            });
			        });
			/*********/
			//
				pianquContainer.setOption({
	            title: {
	                show:true,
	                x:'center',
	                text: ''
	            },
//	             color: ['#4cabce', '#4cacaa'],//
	            legend: {
	                y:'bottom',
	                data:[],
	            },
	            series: [
	                {
	                    name:'',
	                    type:'pie',
	                    radius: ['30%', '40%'],
	                    legendHoverLink: false, //
	                    hoverAnimation:false,//
	                    label: {
	                        show:true,
	                        position:'outside',
	                    },
	                    labelLine: {
	                        show:true,
	                        length:50,
	                    },
	                    data:[],
	                    itemStyle:{
	                        normal:{
	                            label:{
	                                show: true,
	                                formatter: '{b} : {c}({d}%)'
	                            },
	                            labelLine :{show:true}
	                        }
	                    }
	                }
	            ]
	        });
	
	        pianquContainer.showLoading();
	        this.$http.pianquVisit().then(function(res){//
	            pianquContainer.hideLoading();
	            pianquContainer.setOption({
	                legend: {
	                    data: res.data.category
	                },
	                series: [
	                    {
	                        name: '',
	                        data: res.data.pie,
	                    }
	                ]
	            });
	        });
          /******/
			
		}
	}
</script>

<style>
	#echartMarket{
		max-width:1100px ;
		height: 100%;
		margin: auto;
		/*border:1px solid #3762BC*/
	}
	#seaseonContainer,#monthContainer,#diquContainer,#pianquContainer{
		margin: 20px;
		display: inline-block;
		/*float: left;*/
		/*border:1px solid red;*/
	}
</style>


